<template>
    <div class="content-page-1">
         <div class="photo-1"><img :src=photo_url[+message] alt=""></div>
        <component :is="comName2" ref="ddd" class="d3-1"></component>
        <!-- <div>{{ message }}</div> -->
    </div>
   
</template>

<script>
// import JsTagCloud from 'js-tag-c'
// import { number } from 'echarts';
// import {defineProps} from 'vue';
import word3D_1 from '@/components/3D/3D_1.vue';
import word3D_2 from '@/components/3D/3D_2.vue';
import word3D_3 from '@/components/3D/3D_3.vue';
import word3D_4 from '@/components/3D/3D_4.vue';

export default {
    props:{
        message: {
            type: String,
            required :true
        }
    },
    components:{word3D_1,word3D_2,word3D_3,word3D_4},
    mounted(){
        this.$refs.ddd.setTags('3d_1')
    },
    data(){
        return{
            photo_url: [
                'https://design-1329452382.cos.ap-guangzhou.myqcloud.com/img/%E5%90%B9%E7%AE%A1%E4%B9%90%E5%99%A8%E5%B0%81%E9%9D%A2.f972b39b.png',
                'https://design-1329452382.cos.ap-guangzhou.myqcloud.com/img/%E5%BC%B9%E6%8B%A8%E4%B9%90%E5%99%A8%E5%B0%81%E9%9D%A2.05ddfc65.png',
                'https://design-1329452382.cos.ap-guangzhou.myqcloud.com/img/%E6%89%93%E5%87%BB%E4%B9%90%E5%99%A8%E5%B0%81%E9%9D%A2.205713fc.png',
                'https://design-1329452382.cos.ap-guangzhou.myqcloud.com/img/%E6%8B%89%E5%BC%A6%E4%B9%90%E5%99%A8%E5%B0%81%E9%9D%A2.a31e449d.png'
            ],
            comName2: this.findComName(this.message),
            // props: defineProps({
            //     message:String,
            // }),
            targetObject: {setTags:() => {} }
        }
    },
    methods: {
        findComName(message){
            switch(message) {
                case '0': return 'word3D_1'
                case '1': return 'word3D_2'
                case '2': return 'word3D_3'
                case '3': return 'word3D_4'
                default: return 'word3D_1' // 默认值
            }
        },
    },
    watch: {
        message(message){
            this.comName2 = this.findComName(message)
        }
    }
}
</script>

<style>
    .content-page-1 {
        /* position: relative; */
        width: 90%;
        height: 700px;
    }
    .photo-1 {
        position: absolute;
        left: 0;
        top: 50px;
        width: 40vw;
        height: 60vh;
    }
    .photo-1 img {
        width: 100%;
        height: 100%;
    }
    .d3-1 {
        position: absolute !important;
        right: 0;
        top: -20px;
        width: 37vw;
        height: 37vw;
    }
</style>