    data() {
        return {
          count: 0,
          activeName: 'first',
          cssheader: 'el-collapse-item__header',
          csspd: 'el-collapse-item__content',
          csscoll: 'csscoll',
          csstext: 'csstext',
            showDialog: false,
            selectedId: null,
            activeCollapse: '1',
            dataSource: [],
            // dataName: [
            //   '1',
            //   '2'
            // ],
          ds1:
              [
                [
                  {title: '简介', content: '笙是由若干竹管和铜制的簧片构成。是我国汉族古老的簧管乐器。它的音色和演奏出来的声音特别富有悦耳的感染力，这是它的簧片通过空气穿流后、引起的共振而产生的柔美之声。这个乐器的特点还有演奏控制技巧的不同所得到的不同效果，强弱时对比特别明显，同时发音可达到三个以上，有和声乐器之称。可以说它的音响魅力可与高贵、美丽等词相并，能使人联想到古代宫庭。该乐器由多种簧管之方式组成，如十三、三十七簧都有 ，早在南北朝就被广泛使用。'},
                  {title: '发展历史', content: '在中国古代乐器分类中，笙为匏类乐器。《诗经》的《小雅.鹿鸣》写道：“我有嘉宾，鼓瑟吹笙。吹笙鼓簧，承筐是将”，可见笙在当时已经很流行了。从战国到汉代的文献中，共同记载着笙和竽两种同类乐器。《周礼·春官》中有；“笙师，……掌教歙竽、笙、埙、籥、箫、篪、篴、管。”“笙”为官名，其职务是总管教习吹竽和笙等乐器。竽和笙的区别是：笙体小、簧少；竽体大、簧多。二者早期都是用嵌簧的编管插入葫芦内，并以葫芦作为共鸣体，故一些文献认为竽就是不同形制的竽，如《吕氏春秋. 仲夏纪》高绣注：“竽，笙之大者”。春秋战国时期，笙竽是重要的吹奏乐器。竽还一度在宫廷、贵族或市民中广泛地流行。东汉时期的《说文解字》记载竽为36簧。长沙马王堆一号汉墓中出土了一支竽，26管，并设有折叠管，用于产生低音，簧是用铜片制成的。从出土的西汉百戏陶俑和东汉石刻百戏画像中，也可看出竽在百戏乐队中占有重要地位。隋唐时期竽还存在，但在九、十部乐中已不用，而笙在隋九部乐和唐十部乐中的清乐、西凉乐、高丽乐、龟兹乐中均被采用。唐代涌现出许多演奏笙的名家，他们的技艺都达到了较高的水平。唐代不少诗人还为笙写下了诗作。到了宋代，竽则销声匿迹，在教坊十三部中，只有笙色而无竽色。'},
                  {title: '制作流程', content: '笙的构造是：将铜制的簧片装在若干竹管下端，这些竹管插在一个匏制、木制或铜制的带有吹孔的斗 子上。吹时用指按着竹管下端所开的孔，使簧片与管中气柱发生共鸣而发出乐音。演奏时，除单音外、大 都用二音、三音或四音配成和音。早期的笙为竹制，后来改为铜制。明清时期，民间流传的有方、圆、大 、小各种不同的笙的形制。解放后，笙类乐器得到进一步的改进。出现了二十一簧、二十四簧、三十六簧 以及及扩音笙、中音抱笙、键盘排笙等，形成各音区的系列笙的建制，在大型民族乐队中起着重要作用。小排笙、排笙（键盘笙）等多种形制。笙的吹奏技术也有较大发展，除了用于伴奏、合奏外，已发展成为 独奏乐器。笙的形制主要由笙斗、笙管、笙簧三部分组成。笙斗有方形、圆形两种，上插笙管，并通吹口 。笙管多为竹制，现为铜质。吹奏者将气流吹入笙斗，通过笙管是笙簧振动而发音。'},
                  {title: '声色特点', content: '笙的音色甜美、柔润、音量较小，变化幅度不大，而色泽明亮。笙能吹单音，也能吹和音（二至四音），最常见的是平行八度、四、五度、有时也能吹奏三度或和弦。故笙大多常用来为唢呐、笛子伴奏。或在乐队中作“溶合剂”，调和各族乐器的音色，饰润、软化某些独奏乐器的个性。笙的独奏曲业有很大发展。'},
                  {title: '代表作品',content: '《凤凰展翅》、《草原骑兵》'}
                ],
                [
                  {title: '简介', content: '芦笙，为西南地区苗、瑶、侗等民族的簧管乐器。 发源于中原，后传入少数民族地区，其前身为汉族的竽。在贵州各地少数民族居住的村寨，素有“芦笙之乡”、“歌舞之乡”的称誉，是少数民族特别喜爱的一种乐器之一，逢年过节，他们都要举行各式各样、丰富多彩的芦笙会，吹起芦笙跳起舞，庆祝自己的民族节日。广西苗族传说，古时人间五年节无芦笙，遂向天上去要。先派喜鹊去，没要来。又派蝉去。蝉抠出内脏交公鸡看管，上天学会芦笙曲，将年节和芦笙带回来。人们忙过年节，不理芦笙。芦笙便又回天上。蝉向公鸡要内脏，公鸡说巳吃了，只好往外吐，整天咕咕叫，吐不出来。蝉无内脏，不愿再上天要芦笙。人允许它吃露水，遂多次要芦笙。天上不肯给。蝉暗地记住芦笙构造，回来让人仿造，就有芦笙流传。'},
                  {title: '发展历史', content: '芦笙，其前身为中原汉族的竽，后唐朝时传入少数民族地区。唐朝时贵州的少数民族人民开始制作芦笙，并涌现了不少的优秀芦笙吹奏家。古代进京朝贡者，就曾带着芦笙到宫廷演奏过，得到朝廷官员的高度赞赏。20世纪50年代以来，各地文艺工作者对芦笙进行了改革。由原来的六管逐渐增至18管和20管等：把竹筒共鸣器改为薄铜皮共鸣器。这样，扩展了音域，增加了音量，转调方便。演奏法方面，借鉴了单吐、双吐、三吐及颜音等技巧，丰富、提高了芦笙的表现力。随着时代的变迁，芦笙的形状和演奏技巧，除保持了原有的风格外，有了新的改进。如今的芦笙有六管、十管及十二管的，其长度有二尺、五尺及一丈多的；其芦笙曲调，除保持原来的古朴、悠扬之外，曲调多变，节奏明快，特别是伴之深沉、雄浑的芒筒声；使芦笙的声响和音量加重，格外委婉动人。其跳和舞姿的变化更大，不但跳步踢腿刚健有力，而且舞姿潇洒自如，动作优美。'},
                  {title: '制作流程', content: '芦笙由笙斗、笙管、簧片和共鸣管构成。制作时，将整块毛坯料从中破为两半，分别挖掏出内膛，待装入笙管后再用胶粘合，外部用细篾箍五至七圈而成。笙斗又称气箱，多用杉木、松木或梧桐木制作，以杉木最佳，纹理顺直、质地松软、少疤节，外观呈纺槌形，长46厘米～56厘米、宽4厘米～9厘米高3.5厘米～8厘米，细端再接一根长15厘米左右、外径1.8厘米左右的竹管为吹口。。笙管多用白竹制作，白竹的竹径细、竹节长、粗细匀、竹壁薄，通常在直径1.2厘米左右、每节长40厘米～50厘米，是制作笙管的良材，要选生长三年以上、冬至到立春前砍伐的为佳，这时的竹管竹质坚韧、表面光亮、不易虫蛀。簧片多用响铜制作，《尾蕉丛谈》一书载有“长管之上冒以匏，短管之中置以簧，用响铜为之，恒用火炙，亦古制也。”尺寸依音高而定，以c、c1、c2三音为例，长度分别为4厘米、3.5厘米、2.5厘米，宽度分别为0.25厘米、0.2厘米、0.15厘米，余者类推。'},
                  {title: '声色特点', content: '苗族的芦笙乐队，是民间业余奏乐组织。每队由五六支芦笙组成，分为高、中、低不同音区，既有笙管高达三丈有余的低音芦笙，也有管高尺余的高音芦笙，再加上发音低沉的芒筒，合奏起来音响雄壮粗犷、气势磅礴。'},
                  {title: '代表作品',content: '芦笙节：苗族芦笙节，舟溪芦笙节;著名乐曲：《诺德仲之歌》、《大悲调》、《和调》、《赛调》'}
                ],
                [
                  {title: '简介', content: '排笙是解放后根据芦笙的原理改革发展而成的。'},
                  {title: '发展历史', content: ''},
                  {title: '制作流程', content: '排笙有三十个管，用键盘弹奏，用脚踏风箱吹气'},
                  {title: '声色特点', content: '排笙的30个管增大了音量，扩展了音域，是乐队中的低音管乐器。'},
                  // {title: '代表作品',content: '芦笙节：苗族芦笙节，舟溪芦笙节;著名乐曲：《诺德仲之歌》、《大悲调》、《和调》、《赛调》'}
                ],
                [
                  {title: '简介', content: '笛子，又称竹笛。顾名思义，是由竹子制成。上面开有吹孔和膜孔各一个，按指孔六个．它音色清脆，音域宽广，能吹 两组多的音。常规的笛子有两种，即曲笛和梆笛，为民间常用的乐器之一。曲笛善于表现悠扬委婉的情致，具有浓厚的江南韵味。而梆笛短于曲笛，它的音色高而明亮，善于表现刚健、豪放、活泼、轻快的情致，具有强烈的北方色彩。竹笛流传地域广大，品种繁多。使用最普遍的有曲笛、梆笛和定调笛。还有玉屏笛、七孔笛、短笛和顺笛等。'},
                  {title: '发展历史', content: '古称横吹、横笛。为汉族民间常用的乐器之一． 早在汉代已出现竹制笛，汉代笛多为七孔，公元前一世纪末汉武帝时，笛称之为横吹，在鼓吹乐中占有重要地位。北周和隋 代始有横笛之名。唐宋时期的笛均无膜孔。横笛有膜孔的记载见于《清史稿乐志》：“笛，截竹为之，皆间缠以丝，两端加龙首龙尾，左一孔，另吹孔，次孔加笛膜”。使用笛膜后，笛的音质、音量都有了明显变化。笛子虽然短小简单，但它却有七千年的历史。大约在四千五百多年前的时候，笛子由骨制改为竹制。在公元前1世纪末汉武帝时，笛子称为“横吹”,它在当时的鼓吹乐中占有相当重要的地位。从7世纪开始，笛子又有了改进，增加了膜孔，使它的表现力有了很大的发展，并且演奏技术也发展到相当高的水平。到了10世纪，随着宋词元曲的崛起，笛子成了伴奏吟词唱曲的主要乐器，在民间戏曲里，笛子也是不可缺少的乐器。'},
                  {title: '制作流程', content: '古代笛多为竹制，但也有铁笛、钢笛、瓷笛、玉笛，玉笛常见于唐宋以来诗文之中。故宫博物院和中 国艺术研究院音乐研究所均藏有清代龙头笛，笛身涂红漆，上面描绘金龙。现代常用的笛多用紫竹、凤眼竹（斑竹）、湘妇竹、黄枯竹、长茎竹、梅鹿竹等制作，长度依调高而定，内膛去节中空，管身上开吹孔 、膜孔各一个，按音孔六个，音色清脆，音域较宽，常被作为旋律主奏乐器，表现力很丰富。能吹出两组 多一点的音。前出音孔一个和后出音孔二个。吹孔七端管内堵有软木制的笛寒，笛寒至吹孔一段的管身内 膛称海底或笛脑。前出音孔可用以调音，后出音孔距笛尾最近，可美化音色、增大音量，也可系飘穗之用。解放后，对笛进行了多次改革，先后制出了成套的十二调的笛，并在曲笛的基础上改制了加键笛，保留原有的六个音孔，用键子控制新增的半音，便于临时升降半音及转调。'},
                  {title: '声色特点', content: '竹笛的发音清脆嘹亮，充满活力。特别擅长演奏优美抒情的旋律，常在器乐合奏中担任领奏。笛子音色清脆、明亮，音量较大，技巧丰富，表现力很强，是我国吹奏乐器中一件重要的乐器。笛子可演奏各种不同的风格、情绪的乐曲，可独奏、重奏与合奏。在西洋乐队中，有时作为色彩乐器，可获得独特的效果。梆笛（短笛）发音高亢奔放，音区较高。曲笛（长笛）发音优雅润厚，音区一般比梆笛低四度。'},
                  {title: '代表作品',content: '《早晨》、《鹧鸪飞》、《三五七》、《姑苏行》、《喜报》、《今昔》、《帕米尔的春天》、《荫中鸟》、《喜相逢》、《五梆子》、《小放牛》、《百鸟引》'}
                ],
                [
                  {title: '简介', content: '排笛是一种由2～4根扎在一起的长短不同的笛子组成的乐器，由赵松庭于20世纪60年代初发明。相比传统笛子，排笛的音域更广，可达三个八度以上，音色和表现力也更丰富。'},
                  {title: '发展历史', content: '由赵松庭于20世纪60年代初发明。'},
                  {title: '制作流程', content: ''},
                  {title: '声色特点', content: '排笛和单一笛子的吹奏方一样，就是绑在一起演奏的时候，需要找到合适的切换手法和技巧。需要多排练几次，慢慢适应不同笛子的口风口劲及手法要求。'},
                  {title: '代表作品',content: '《采茶忙》、《婺江风光》、《荫中鸟》'}
                ],
                [
                  {title: '简介', content: '侗笛，是侗族独特的吹口气鸣乐器。侗语称介各、济各斯。又称各笛、草笛。流行于贵州省黔东南苗族侗族自治州榕江、从江、黎平、广西壮族自治区三江侗族自治县、湖南省通道侗族自治县和黔、桂、湘三省毗邻的广大地区。侗笛外形和洞箫相似，吹口装有簧片，竖吹，音色清丽悠扬，既可表现婉转抒情的情调，又可奏出热烈欢腾的旋律。富有山野风味，常用以独奏或为歌唱伴奏。'},
                  {title: '发展历史', content: '关于侗笛的起源，除侗族古歌中有：“地平金富人造笛”外，在侗族韵文叙事诗歌《祖公河上》中也载有：在“苗娘(姑娘名)还不会打扮，银汉(男子名)也不会玩，年老的不会结亲家 ，姑娘们不会嫁丈夫”的年代里，“金转造芦笙，金烧造笛子……，姑娘听了笛声跟着唱……。”这就是民间流传的有关侗笛和笛子歌的传说。在黔、桂、湘三省广大侗族地区，峰峦 呈秀、溪流错综，在这山清水秀、如诗如画的自然环境中，聚居着勤劳勇敢、多才多艺的侗族人民。'},
                  {title: '制作流程', content: '传统的侗笛，管身多用水竹制作，也有用紫竹、金丝竹或黄枯竹制作的，长短、粗细规格不定，一般管长30厘米～40厘米、内径1.3厘米～1.5厘米。上端除有坡形吹口外，并留竹节或堵以笛塞，下端敞口，管身上开有六个按音孔，在近上端吹口处，开有一个装着竹片的哨孔。制作侗笛时，一般使用8月砍下的黄枯竹或紫竹，要求选用的竹节距离长且粗细均匀，经阴干或蒸干后，在较粗的一端开坡形吹口，距吹口5厘米处开一方形哨孔，孔上方管壁破开，形成深1毫米、宽5毫米左右的长方槽。竹糟两侧垫竹条，上盖竹片并用竹蔑固定，形成进气道。 簧片采用薄竹片制作，用竹蔑固定在哨孔下半部，形成笛哨。音孔开在在哨孔以下的管身上，第六孔至管口距离，以管口下端周长为度，在哨孔至第六孔的 1/ 2处开第一孔，第二至第四孔等距排列，第五孔离第四孔较近，音域可达两个八度。与此形制相同的还有佤族的瓦格洛，苗族的姊妹箫，塔吉克的竖笛等。'},
                  {title: '声色特点', content: '侗笛的音色柔美、明亮，介于竹笛与洞箫之间，明朗而不浮华，甜美而不消 沉，并富有浓郁的山野风味。演奏中常运用独特的指颤音和滑音技巧。常用于独奏、合奏或 伴奏。但由于音域窄、音量小，一般只适宜为声乐伴奏和演奏简单的独奏曲。在黔东南黎平 县的南部和桂北三江侗族自治县，侗笛主要用来伴奏“小歌”(笛子歌)。'},
                  {title: '代表作品',content: '《火车开进侗山寨》、《刘三姐》'}
                ],
                [
                  {title: '简介', content: '口笛，竹乐器，起源于黄帝时期，在1971年初，由竹笛演奏家俞逊发先生发明创造。后来，俞逊发为增加两孔口笛的音域，又增加了三个指孔，从而研制成功了五孔口笛。又有不少人对口笛进行了改进，有的增加开孔可以转调方便或增加音域，有的改进指孔大小，可以减少半孔指法，还有的采用不同材料制作口笛；这些改进不断丰富着口笛的表现力，有两孔口笛、五孔口笛、七孔口笛、音阶口笛和丁笛等。'},
                  {title: '发展历史', content: '黄帝时期，即距今大约4000多年前，黄河流域生长着大量竹子，开始选竹为材料制笛，《史记》记载：“黄帝使伶伦伐竹于昆豀、斩而作笛，吹作凤鸣”，以竹为村料是笛制的一大进步，一者竹比骨振动性好，发音清脆；二者竹便于加工。秦汉时期已有了七孔竹笛，并发明了两头笛，蔡邕、荀勖、梁武帝都曾制作十二律笛，即一笛一律。笛在古代称为“篴”。到了汉代，许慎的《说文解字》有：“笛，七孔，竹筩也”的记载。1978年，从湖北随县曾侯乙墓出土了两支竹篪，从湖南长沙马王堆三号汉墓出土了两支竹笛，出土的篪和古籍中记载的汉篪，除长度略有出入外，其他形制完全相同；出土的笛除与记载相同外，并在墓内的竹筒上写有篪的字样，显然是古代竹笛，古代的篪和笛非常相似，历来有人篪、笛不分，说成是同一乐器，实际是有区别的。从出土篪、笛可以看出：篪， 6孔，闭口，能奏五声加一变化音，全身髹（xiū）漆；笛，7孔，开口，能奏七声加两个变化音，不髹漆。'},
                  {title: '制作流程', content: '口笛构造简单，制作简便，多使用黄枯竹或小水竹制作，也可使用长茎竹、凤眼竹、紫竹或其他材料制作，以杆身圆直、竹质坚硬的为佳。取一段短而细的竹管，体空，右端为第一孔，左端为第二孔，左、右端内径分别为10.5和11毫米，在距左、右端分别为23 和25 毫米处开一吹孔，即制成两孔口笛。五孔口笛是在靠近两孔口笛吹孔的管壁上再开3 个音孔，五孔口笛，管长7 4 毫米，左、右端内径分别为9.5 和10 毫米，吹孔距左、右端分别为34.5 和39.5 毫米，第三、四、五孔，开在距吹孔外缘14 毫米的一条直线上。吹孔开成椭圆形，长径8 毫米，第三、四、五音孔为圆形，直径均为5 毫米。'},
                  {title: '声色特点', content: '口笛音色明亮、高亢，近似哨音，穿透力强。特别适宜表现优美的旋律及活泼热烈的欢快曲调，并能生动形象地模仿百鸟争鸣和人声。在民族乐队中，常用它来演奏华彩乐段，可用于独奏、合奏或伴奏。此外还能参加西洋管弦乐队合奏，是具有独特效果的色彩性乐器。'},
                  {title: '代表作品',content: '《云雀》'}
                ],
                [
                  {title: '简介', content: '田螺笛原属靖西壮族民间的儿童玩具，在靖西县民族博物馆陈列多年，而挖掘壮族拟声田螺笛则是2000年前的事。2002年10月，靖西县组织壮族博物馆和当地民间艺人根据有关壮族民间口碑和地方志记载，通过一段时间的艰苦摸索，从多试式样、多试品种、多试音质等大量的策划和改进工作，终于试制出音色浑厚韵律优美的田螺笛。'},
                  {title: '发展历史', content: '2001年7月，韩国民俗文化考察团到靖西，还专门对田螺笛和葫芦胡等民间民俗文化进行探讨和交流。2002年11月，毛泽东的儿媳邵华将军到靖西考察，拍下了田螺笛的照片，在京媒体上发表，引起了广泛关注。'},
                  {title: '制作流程', content: '田螺笛的制法很简单。只要选择大个田螺，剪掉尾部尖端处为出音孔，去掉内肉，在离大口处约2厘米处开一笛孔贴上笛膜即可。'},
                  {title: '声色特点', content: '吹奏时嘴贴笛口，模拟歌曲调子进行吹唱，声音经笛膜震荡，变成笛音由出音孔传出，音质既像男低音，又具有喉管乐器的声音。田螺笛可吹奏民间曲调和壮剧、巫伦等曲调，与壮族马骨胡、葫芦胡、葫芦琴搭配演奏音色更佳。'},
                  // {title: '代表作品',content: ''}
                ],
                [
                  {title: '简介', content: '骨笛是笛子的一种，也是最早的乐器。又称鹰笛或鹰骨笛，藏族、塔吉克族、柯尔克孜族边棱气鸣乐器，藏语称当惹。用鹫鹰翅骨制成，流行于西藏、青海、云南、四川、甘肃省等的藏族牧区。常用于独奏。是藏族青年喜爱的自娱性吹奏乐器，多在夏季放牧或田间劳动休息时吹奏自娱。'},
                  {title: '发展历史', content: '从约九千年前的河南贾湖遗址中出土的一批精致骨笛，近被专家认定为世界上最早的吹奏乐器。分属于贾湖早中晚三期的二十多支五孔、六孔、七孔和八孔骨笛，经专家研究已具备了四声、五声、六声和七声音阶，把中国七声音阶的历史提前到八千年前。'},
                  {title: '制作流程', content: '骨笛管身多用鹫鹰的翅膀骨制作，也有用仙鹤的腿骨制作的。骨笛规格不一，长短、粗细各不相同，通常比竹笛短而细，一般翅骨笛管长25厘米左右，鹤腿笛管长29厘米左右，管径2厘米左右。管身上口密封，只留一窄缝作为吹孔，管端的背面斜开一发音孔，管身正面下部开有六个按音孔（或开成前五后一）。'},
                  {title: '声色特点', content: '"由于鹰笛所用鹰骨的不同，在音响上也有区别：用鹫鹰骨做的鹰笛，骨质坚硬，骨纹细密，表面光润，骨管较长而粗，音调偏低．音色浓厚；用老鹰骨做的鹰笛，骨质不如鹫鹰骨，骨管较短而细雨，音调偏高，音色明亮。鹰笛可以独奏、合奏或为歌舞伴奏。'},
                  {title: '代表作品',content: '《春播》、《上山》、《下山》'}
                ],
                [ 
                  {title:"简介",content: "箫又名洞箫，亦称“篴”,“篪”,“籥”等。古时箫并不直接称为“箫”，魏晋时期将竖吹六孔单管箫称为“笛”，至唐代仍用此名，宋代称箫为“箫管”，宋元以后才将排箫、箫管（洞箫）浮雕之吹箫者和横笛区分开来，至此“箫”才专指洞箫，才有了单管箫的含义，清代的箫已经与现在使用的箫基本一样了"},
                  {title:"发展历史",content: "箫的产生，其历史可以追根溯源到远古时期。中国考古学表明，目前出土文物中发现了有距今七千多年的骨质发声器，考古学家称之为“骨哨”(浙江河姆渡出土的文物，现存浙江博物馆)。这批骨哨是用鸟禽类中段肢骨制成的，古代人将骨骼中的骨髓抽掉，形成一支中空的骨管，然后在管壁上打有孔洞，它们长7厘米左右，管径6--8毫米，略呈弧曲形，在凸弧面上开有二或三孔，可以吹出几个音来，于是就形成了骨哨。在虞舜时代，曾出现过一部称之为“箾韶”的古代乐舞，“箾”即是今天的“箫”字。因为这部乐舞主要是用古代排箫来演奏的，所以我们认为《韶》的演奏使箫进入了一个新时代。《大夏》是古代人民歌颂大禹治水为内容的古代乐舞，它共分为“九成”(九段音乐)，用“籥”伴奏，又称为“夏竹九成”。竹就是排箫的前身。由此可见从《箾韶》到《大夏》，箫曾在中国音乐史上经历过多年的辉煌时代。周代，曾将我国古代乐器分为“八音”，它们分别是：金、石、丝、竹、匏、土、革、木八类乐器，其中“竹”就是指箫和篪。自从国初期的曾侯乙墓的出土，箫做为古代乐器的实物才得以我们亲眼所见。因此提起“丝竹”二字，人们自然会想到这种乐器。"},
                  {title:"制作流程",content: "箫的构造较简单,用紫竹、黄枯竹或白竹制作,全长70~90厘米,比曲笛稍长而细,管身内径1.2~1.4厘米,上端留有竹节,下端和里面去节中空。吹口开在上端边沿,由此吹气发育音。在箫管中部,正面开有5个音孔,背面有1个音孔,用以控制音的高低，起着美化音色增大音量的作用，也可拴系飘穗（装饰绦带）用。在出音孔下面的两个圆孔为助音孔，它起着美化音色和增大音量的作用，也可用此孔栓系箫穗为装饰。箫不设膜孔，专业演奏的箫音孔增至八孔，并带有铜插口，可调节音高音低，方便于乐队的合奏。"},
                  {title:"声色特点",content: "箫的演奏技巧与笛类似，但不如笛灵敏。不宜演奏花舌、垛音等表现富有特性的技巧，而适于吹奏悠长、恬静、抒情的曲调，表达幽静，典雅的情感。箫不仅适于独奏、重奏，还用于江南丝竹、福建南音、广东音乐、常州丝弦和河南板头乐队等民间器乐合奏，以及越剧等地方戏曲的伴奏。在古曲《春江花月夜》中，一开始洞箫奏出轻巧的波音，配合琵琶模拟的鼓声，描绘出游船上箫鼓鸣奏的情景，在整个乐曲中，箫声绵绵，流畅抒情。"},
                  {title: '代表作品',content: '《春江花月夜》、《妆台秋思》、《平湖秋月》、《梅花三弄》、《平沙落雁》、《清明上河图》、《故园旧梦》'}
                ],
                [
                  {title: '简介', content: '排箫，传统乐器，迄今发现的世界上最早的排箫是距今3000年的中国西周初期的骨排箫。古代的排萧以参差不齐的多个小竹管依其长短顺序并排，并用木框镶起来。排列竹管的样式有多种，如一端长，一端短，上端吹口取齐，如樱形；有的中部短，两端长，上端吹口取齐，有如燕尾形：有的则取长短相等的竹管，排成矩形或扁面形。排萧一管发一音，音高依竹管长短及向管内灌蜡后管的深浅不同而定。演奏姿势双手握排策两端，吹口一端向上，置于下唇处，口型有如吹笛，移动排萧使气流通过不同管口发出不同音高。排萧在历史上除单独演奏外，多用于声乐或歌舞伴奏、器乐合奏，民间已很少使用。'},
                  {title: '发展历史',content: '排箫是中国古代一种重要的乐器，其历史可以追溯到远古社会末期，最早的记载与舜时的乐舞《韶》有关。排箫在西周至战国时期得到了显著发展，成为雅乐中的重要礼器，并在汉代达到鼎盛，广泛应用于宫廷雅乐、军乐和民间文化活动中。魏晋南北朝时期，排箫的发展受到社会动荡的影响，但仍与佛教文化结合，展现出新的特点。唐代是排箫发展的第二个高峰，尤其在佛教音乐中达到了顶峰。元明清时期，排箫逐渐衰落，仅在雅乐系统中保留。鸦片战争后，随着封建制度的瓦解和西方音乐的传入，排箫几乎被遗忘。中华人民共和国成立后，排箫重新受到关注，1956年被确定为中国的乐徽，成为代表中国音乐的符号。1978年湖北曾侯乙墓出土的排箫进一步证实了其历史地位。1981年，吉林省歌剧院研制出双排加键排箫，拓展了其音域和表现力。'},
                  {title:"制作流程", content: "不同排箫因材料、种类不同，吹管的联接方式也不相同，有的用绳子、竹篦片固定，有的用木框镶起来，有的用胶粘合在一起，有的用底座固定。有的塑料排箫是一体的，是加工时一次成型的。以一支悠悠竹子排箫的制作过程举例，从砍下竹子到作为成品出厂一般需要经过以下几步：选材、烤直、测量定长、刨面、粘管、打磨、喷漆、调音、打标出厂，木制排箫则用专业车床将管子按标准尺寸车成木管，之后的步骤与竹排箫一样。"},
                  {title:"声色特点", content: "排萧的音色柔和、典雅，低音区发音深沉、哀郁，有很强的穿透力和震动力。特别是弱奏时的余音，袅袅不绝，给人“如怨如慕、如泣如诉”之感。中音区音色圆润、优美，柔中有刚，温婉中又含有内在的力度，适合表现明朗空阔的意境。高音区发音清越明亮，但稍嫌紧张锐利。一般说来，箫多使用中、低音区，而较少用高音区。"},
                  {title: '代表作品',content: '《在水一方》、《独上西楼》、《橄榄树》、《再见李兰香》、《同桌的你》、《孤独的牧羊人》'}
                ],
                [
                  {title: '简介', content: '在海南黎族的六十多万黎族同胞有一种独特的用鼻子来吹奏的箫，故称为鼻箫，是黎族富有特色的边棱气鸣乐器，因用鼻孔吹奏而得名。黎语称虽劳、屯卡、圆哈。'},
                  {title: '发展历史', content: '边棱音气鸣乐器，箫用鼻吹故称。吹孔设於管端节隔中央，流行海南岛黎族，黎族语也称"虽劳"、"屯卡"、"拉里各丹"。历史久远，一千多年前已在我国海南岛民间流传。'},
                  {title: '制作流程', content: '箫管用石竹制作，其长短、粗细规格不一，民间多使用一根无节的细竹管，管长60厘米～70厘米、 管径 1.6厘米左右，在距两端管口8厘米处，各开一个圆形按音孔。按音孔既可开 在管身一侧， 也可开成前后各一。如果使用两端带竹节的竹管，需在节隔中心开一圆形通孔；使用多节竹 管制作，则要打通竹节。吹孔在竹管的细端。'},
                  {title: '声色特点', content: '鼻箫有许多固定的曲调，它们大都是无标题的抒情短曲，每曲都有一定的含义，在黎族人民中世代相传，但各地也不尽相同。而在黎家青年男女中，鼻箫则多为即兴吹奏，其曲调也因人而异，难怪黎族姑娘可以根据乐曲和音色的不同，能够在很多的箫声中辨认出自己情人独 特的箫声来。"抛个石头探水深，吹曲鼻箫试侬心"。从这句黎谣中也可看出，鼻箫与黎族青年的恋爱生活有着密切关系。黎族姑娘也是吹鼻箫的能手，每逢农闲、节日或恋 爱时，当小伙子吹起嘟噜(洞箫)时，姑娘则吹响鼻箫对答。'},
                  // {title: '代表作品',content: ''}
                ],
                [
                  {title: '简介', content: '塞箫，苗族吹口气鸣乐器。苗语称展尖，意为堵有软木塞的竹管。汉族称其为塞箫。流行于贵州省兴义、安龙，广西壮族自治区隆林、西林、那坡和云南省广南、富宁等黔、桂、 滇三省区接壤的苗族地区。'},
                  {title: '发展历史', content: ''},
                  {title: '制作流程', content: '管身竹制，多采用当地生长的苗语称胸叮的竹子制作，这种竹材管径较细而竹节修长， 是制塞箫的良材。管长一般42厘米，中间无节，两端通透。上端管口外径2.1厘米、 内径1.5厘米左右，下端管口外径1.7厘米、内径1.3厘米左右。制作时，在上口塞进一个 长2.6厘米、直径1.5厘米、劈去一侧的软木塞。木塞多用杉木或泡桐木制作，木塞纹理要 与竹管纹理方向一致。将木塞一侧(圆周的1/4弧面)顺纹劈去，然后塞入管首背面，劈去的部分与竹管内壁就构成一个半月形进气通道。'},
                  {title: '声色特点', content: '用于独奏，所奏乐曲都来自民歌音调，但无固定曲牌 ，只有远调与近调之分。远调是为在远处的恋人而吹的，起音较高，音色脆亮而悠扬；近调 是为身旁的恋人而吹的，起音较低，音色柔和而委婉。近调又有五指调、六指调两 种，前者是固定按住第三孔，用另外五指开闭音孔发音，音域较宽；后者则六指均用，音域 只有十二度。每首塞箫乐曲都含有一定的词意内容，有着独特而完美的表达意境。'},
                  // {title: '代表作品',content: ''}
                ],
                [
                  {title: '简介', content: '姊妹箫是布依族、苗族吹口气鸣乐器。布依语称乌里乌鲁。苗语称占得息、力布 、咪咪。汉语称姊妹箫。流行于贵州省黔西南、黔南、安顺等地区的兴义、惠水 、平塘、荔波、平坝、清镇、贵定、龙里和花溪等地。'},
                  {title: '发展历史', content: '姊妹箫作为中国西南少数民族特色吹管乐器，其历史源流于文献资料中的记载并不明确，且甚少提及。因此，只能从姊妹箫当下存在的民族与流传区域中推测其历史渊源。通过文献查阅与田野考察所得，姊妹箫现多流行于云南曲靖与贵州黔中、黔南、黔西北等苗族、布依族聚居地区。就目前所搜集的文献资料而言，除了王星冶于2003年发表的《六枝落别布依族民间吹管乐器姊妹箫及音乐初探》中对于姊妹箫出现时间的推测为至少应有四、五百年的历史了。以外未有只言片语涉及于此。'},
                  {title: '制作流程', content: '管身竹制，由两支长短、粗细、音高均相同的六孔箫管并列绑扎而成。多使用当地出产 的水 竹制作，规格大小不一，管长25厘米～40厘米、上端内径1.3厘米～1.5厘米，上下 两端管口 通透。箫管采用内塞式进气道，即在上端管口置入一个木塞，木塞一侧削出一条宽、深各0 .2 厘米的沟槽，与管壁形成进气孔道。在距上端管口3厘米处，正对进气孔道开一发音 孔，孔长1厘米、宽0.3厘米。发音孔多设在管身的背面。管身正面下部开有 六个圆形按音孔，孔距相同。发音孔至第六孔的距离，与第六孔至第一孔的距离相等。'},
                  {title: '声色特点', content: '姊妹箫发音轻柔抒情，高音明亮，低音柔和。常用于独奏。也可合奏或为民歌伴奏，乐曲 多为唢呐曲牌。姊妹箫是青年男女恋 爱时离不开的吹奏乐器，常以箫声传情达意，乐曲有《喊娘调》、《陈情小调》等，每曲皆 有唱词，曲调优美抒婉，情意绵绵。'},
                  {title: '代表作品',content: '《迎亲调》、《接客调》、《离娘调》、《喊娘调》、《陈情小调》'}
                ],
                [
                  {title: '简介', content: '筚篥（bì lì），也称管子。中国传统双簧管乐器，即觱篥 [1]。多用于军中和民间音乐。流行于我国各地，为汉族、维吾尔族、朝鲜族等多民族所喜爱。'},
                  {title: '发展历史', content: '筚篥（bì lì），也称管子。中国传统双簧管乐器，即觱篥 [1]。多用于军中和民间音乐。流行于我国各地，为汉族、维吾尔族、朝鲜族等多民族所喜爱。'},
                  {title: '制作流程', content: '起初筚篥是用羊角和羊骨制成，而后改由竹制、芦制、木制、杨树皮制、桃树皮制、柳树皮制、象牙制、铁制、银制等等，而以竹制最为普遍，制作较易。我们常看到孩子们用柳枝作玩具，其制法是将手指粗的柳枝抽去其中的柳骨。保留着完整的柳皮，成一空筒，也可以管腔上穿孔，即成为柳皮筚篥。推想起来，若用桃枝制作，也就是桃皮筚篥了。'},
                  {title: '声色特点', content: '筚篥的音色或高亢清脆，或哀婉悲凉，质感鲜明，在法曲等演奏中常为领奏器乐。经丝路作为胡乐的一部分传至中原，也深受人们喜爱。筚篥得以在千余年间普及，其重要的原因是筚篥所具有的独特的艺术功能。筚篥通过气息支撑，可以表现出圆润不断，委婉起伏的持续音。它的音色深沉、浑厚、凄怆，对人们思想感情具有强烈的激发力，特别是表现悲愤、激昂情绪时有独到功能，古人往往借它抒发伤时感事的情怀。'},
                  {title: '代表作品',content: '《美丽的祖国》、《繁荣的和田》'}
                ],
                [
                  {title: '简介', content: '双管是一种古老的中国传统乐器，东汉郑玄注《周礼·春官·小师》："管如篴而小，并两而吹之"。可见当时已存在双管类乐器。一种是东北双管，系将两支管并排扎结一起而成。流行于东北、华北广大地区。现代双管由两支长短、音高相同的管子并排扎结而成。每管八孔（前七后一）。九孔管已很少见到。演奏时，可以两管并吹，也可将两管分开，两手各持一管，手指各按管子上方的三个音孔，交替吹奏短促的音符，以烘托气氛，把乐曲推向高潮。乐曲有《江河水》等。'},
                  {title: '发展历史', content: '东汉郑玄注《周礼·春官·小师》："管如篴而小，并两而吹之"。可见当时已存在双管类乐器。《隋书·音乐志》载龟兹乐中有双筚篥。新疆吐鲁番东北部的伯孜克里克石窟（开凿于550—1368年）第二十九窟有双筚篥图象，双管并列，管身有五个按孔。宋《乐书》中载有双筚篥："胡部安国乐器有双筚篥，《唐乐图》所传也"。又有双凤管："双凤管合两管以足十二律之音，管端施回簧，刻凤以为首，左右各四窍"。表明双管自唐以来已广为应用。'},
                  {title: '制作流程', content: '双管制作材料多采用老红木，也有用铜管及南柞木制的，管两头用锡铅溶化镶上双边防止裂痕。很少用竹制（因身短易裂）采用老红木制做的双管音色纯正清脆。双管由音箱、音管和簧片构成。音箱多用一个无腰的整葫芦制作，底部钻孔掏去籽粒，葫芦柄处插一截细竹管为吹口。'},
                  {title: '声色特点', content: '吹奏时，持续音犹如一条长长的彩带高飘在上方，使旋律特别丰满。流行于临沧地区镇康县的渥格宝，不仅形制与耿马傣族的双管筚朗叨相似，其主、副管的筒音也完全相同。筚朗叨主管的音色柔润而纤秀，在副管持续音的衬托下，有着一种含蓄而朦胧的美感。尤其是它吹出的颤音犹如丝绸抖动，飘逸而轻柔。故此，从前曾有人误称它为葫芦丝。演奏技巧有颤音、吐音和上下滑音等。常用于独奏自娱，也可由大小不同的几支合奏。擅长演奏山歌和表现浓郁的民族风情。'},
                  {title: '代表作品',content: '《喊木麻》、《咧少调》、《山歌调》、《芦子调》'}
                ],
                [
                  {title: '简介', content: '喉管又称"竹管"，传统吹奏乐器，它是在中国民间乐器管子的基础上创制而成的一种双簧气鸣乐器。喉管在问世之初，是广东省的街头小贩招揽生意的一种工具，到了二十世纪20年代末期，它才开始在广东音乐和广东粤剧中应用，后来，普遍地流行于广东和广西地区。'},
                  {title: '发展历史', content: '喉管是中国传统吹奏乐器，由管子发展而来，流行于两广地区。最初为广东街头小贩所用，20世纪20年代末期，始在广东音乐和粤剧中应用；20世纪50年代初，音乐工作者在喉管的基础上，将原来的筒音f 往下移低小三度到d,作为中音喉管，移低到A，作为低音喉管；20世纪60年代出现了加键喉管，不仅可以奏出半音阶，适于转各种调，而且音域扩展到二个半八度，使喉管一跃而成为独奏乐器。'},
                  {title: '制作流程', content: '喉管的构造非常简单，由哨子，管身和喇叭口三个部分组成。它的哨子取材于芦苇，哨口比较宽，两片对合的哨片也比较厚。管身是用长茎竹、乌木、红木、杂木、塑料管或者是金属管等材料制成，其中，以竹制管身的音色最好。喉管的管身上开有七个音孔，管身下端装有薄铜片制成的喇叭口，起到扩大音量和装饰的作用。'},
                  {title: '声色特点', content: '不仅可以奏出半音阶，适于转各种调，而且音域扩展到二个半八度，使喉管一跃而成为独奏乐器。'},
                  // {title: '代表作品',content: ''}
                ],
                [
                  {title: '简介', content: '唢呐是中国的传统双簧木管乐器。是世界双簧管乐器家族中的一员，经过几千年的发展，使唢呐拥有其独特的气质与音色，已是中国具有代表性的民族管乐器。唢呐是中国历史悠久、流行广泛、技巧丰富、表现力较强的民间吹管乐器。它发音开朗豪放，高亢雄壮，刚中有柔，柔中有刚，深受广大人民喜爱和欢迎的民族乐器之一，广泛应用于民间的婚、丧、嫁、娶、礼、乐、典、祭及秧歌会等仪式伴奏。'},
                  {title: '发展历史', content: '公元3世纪，唢呐由东欧、西亚一带传入中国。在明代，古籍中始有唢呐的记载。明代武将戚继光曾把唢呐用于军乐之中。在他《纪效新书·武备志》中说："凡掌号笛，即是吹唢呐。"明代后期，唢呐已在戏曲音乐中占有重要地位，用以伴奏唱腔、吹奏过场曲牌。而在以戏曲音乐为基础的民间器乐中，唢呐也成为离不开的乐器。到了清代，唢呐称为"苏尔奈"。到了近现代，唢呐成为中国人民使用颇广的乐器之一。'},
                  {title: '制作流程', content: '唢呐由哨片（哨子）、气盘、芯子（有的地方叫笛针）、唢呐杆和唢呐碗五部分构成。在木制的锥形管上开八孔（前七后一），管的上端装有细铜管，铜管上端套有双簧的苇哨，木管上端有一铜质的碗状扩音器。唢呐虽有八孔，但第七孔音与筒音超吹音相同，第八孔音与第一孔音超吹音相同。1993年，由著名管乐演奏家郭雅志先生发明了唢呐"活芯"装置，令传统唢呐奏出半音阶、十二音体系，丰富了唢呐的表现力。'},
                  {title: '声色特点', content: '唢呐的音色特点包括高亢激昂、音量较大、穿透力强。它在婚庆、庙会等喜庆活动中能够迅速点燃气氛，营造出热烈的氛围。同时，唢呐也可以演绎哀婉的情感，特别是在丧礼等场合，唢呐的低沉音色常用来表达悲伤与悼念。唢呐的音域较宽，能够演奏复杂的旋律，尤其擅长表现大起大落、急促高亢的音乐情感。'},
                  {title: '代表作品',content: '《百鸟朝凤》、《全家福》、《抬花轿》、《六字开门》、《社庆》、《一枝花》'}
                ],
                [
                  {title: '简介', content: '巴乌，簧管乐器，也叫"把乌"，流行于云南彝、苗、哈尼等民族中。哈尼族称"各比"，彝族称"比鲁"或"乌勒"，侗族称"拜"，常用于独奏或为舞蹈和说唱伴奏。巴乌的品种较多，在哈尼族，有单管、双管之分，由于竹管长短、粗细的不同，还有高音、中音和低音巴乌之分。巴乌音色柔美、浑厚，深受人们喜爱。彝族男青年多在夜晚邀约姑娘歌舞玩耍时用来吹奏。'},
                  {title: '发展历史', content: '原始巴乌由于只有八个音控只能发出九个音调，不能吹出完整的七声音阶。研究者针对这个局限对其音孔排列的进行了改良。以中央民族乐团宁保生为代表力图通过改编原有的音孔序列排法来达到扩大音域的效果。改良后，巴乌可以吹奏一些复杂旋律音域广的曲子，吹奏技巧得到提高。对于扩大音域在1960到1980年间，巴乌的改良得到了更大的发展，这种改良体现在了新品种的出现。'},
                  {title: '制作流程', content: '巴乌的制作比较简单，取一段长约26到28厘米的竹管，在上端近节处的一侧嵌上一片三角形的铜质簧片，竹管上钻开8个按音孔，能发出6个到9个的音。云南民族乐器制作师杨声制成木管巴乌，用紫油木制，设铜插口。'},
                  {title: '声色特点', content: '虽然巴乌的音域窄、音量小，但它独具民族风格，极富民族色彩，吹奏起来，如怨、如慕、如泣、如诉，余音袅袅，不绝如缕，深受哈尼族、彝族人民喜爱。哈尼族人人随身携带，老人和青年们都喜欢用巴乌来表达情意或追忆往事。彝族喜欢用两支同调的巴乌齐奏或对奏。'},
                  {title: '代表作品',content: '彝族的《约调》、哈尼族的《串田地》、《傍晚的声音》、苗族的《情调》、《约会》、傣族的《咧少调》、《赞哈调》、《芒市坝子调》'}
                ],
                [
                  {title:"简介",content: "埙是汉族特有的闭口吹奏乐器，音色朴拙抱素独为地籁，乃是乐器中最接近道家天籁的。在世界原始艺术史中占有重要的地位。埙同壎xūn形声。从土，熏声。本义：古代用陶土烧制的一种吹奏乐器,大小如鹅蛋，六孔，顶端为吹口。又叫“陶埙”) 同本义。埙之为器,立秋之音也。平底六孔，水之数也。中虚上锐，火之形也。埙以水火相和而后成器，亦以水火相和而后成声。故大者声合黄钟大吕，小者声合太簇夹钟，要皆中声之和而已。"},
                  {title:"发展历史",content: "埙是中国最古老的吹奏乐器之一，大约有七千年的历史。原始社会的埙，器形多样，如浙江余姚县河姆渡遗址发掘的陶埙，呈椭圆形，只有吹孔，无音孔，距今约七千年。陕西西安半坡村仰韶文化遗址陶哨，其形略如橄榄，也只有一个吹孔，用细泥捏塑而成，是埙的原始形态之一，距今约六千年。埙经历了漫长阶段，大约在四五千年前，埙由一个音孔发展到两个音孔，能吹三个音。进入奴隶社会以后，埙得到了进一步的发展，在甘肃玉门火烧沟出土的父系社会晚期至奴隶社会初期的埙，有三个音孔，能吹四个音。到公元前1000多年的晚商时期，埙发展到五个音孔，能吹六个音。到公元前700多年前的春秋时期，埙已有六个音孔，能吹出完整的五声音阶和七声音阶了。埙由一个音孔发展到六个音孔，经历了3000多年的漫长岁月。埙从一个音孔发展到三个音孔、五个音孔、六个音孔乃至八、九个音孔，经历了长达3000多年的漫长岁月，其本身也在不断地进行序列性的发展。对古代埙的测音工作，将进一步推进对中国古代音阶的形成与发展这一课题的研究。"},
                  {title: '制作流程', content: '十孔埙的结构为一个类似空心球式的主体。有一个吹孔、九个发音孔。主体上端设有一个吹孔，正面设有七个发音孔，反面设有两个发音孔。不同材料制成的埙音色各有特点，如：陶埙、半瓷埙、瓷埙、木埙、竹埙、紫砂陶埙；不仅在不同材质上有变化，除了传统的卵形埙，还有卵形埙、葫芦埙、握埙、鸳鸯埙、子弹头埙、子母埙、牛头埙、笔筒埙 等多种类型。'},
                  {title: '声色特点', content: '埙之为器,立秋之音也。平底六孔，水之数也。中虚上锐，火之形也。埙以水火相和而后成器，亦以水火相和而后成声。故大者声合黄钟大吕，小者声合太簇夹钟，要皆中声之和而已。'},
                  {title: '代表作品',content: '《哀郢》、《长门怨》、《伯牙吊子期》、《长亭怨慢》、《高山流水》、《关山月》、《阳关三叠》、《苏武牧羊》、《山鬼》'}
                ],
                [
                  {title: '简介', content: '葫芦丝，又称"葫芦箫"，是云南少数民族乐器。葫芦丝的起源可追溯到先秦时期。葫芦丝发源于德宏傣族景颇族自治州梁河县，主要流行于傣、阿昌、佤、德昂和布朗等族聚居的云南德宏、临沧地区，富有浓郁的地方色彩。常用于吹奏山歌、农曲等民间曲调。葫芦丝可分为高、中、低音三种类型。其音色独特淳朴，外观朴实、精致，简单易学，受到许多音乐爱好者的喜爱。'},
                  {title: '发展历史', content: '葫芦丝是由中原的"笙"逐渐演进、改造而来的。笙，历史久远，发源于中原，后传入西南少数民族地区。新中国成立后，中国民族音乐工作者对葫芦丝进行了不断的改革。1958年，云南省歌舞团首先把音域扩展为14个音。北京的一些文艺团体又制成两种新葫芦丝。'},
                  {title: '制作流程', content: '葫芦丝形状和构造别具一格，它由一个完整的天然葫芦、三根竹管和三枚金属簧片做成。整个葫芦做气室，葫芦底部插进三根粗细不同的竹管，每根插入葫芦中的竹管部分，镶有一枚铜质或银质簧片，中间的竹管最粗，上面开着几个音孔，称为主管，两旁是附管，上面只设簧片，不开音孔（指传统葫芦丝），只能发出与主管的共鸣的和音。阿昌族的葫芦丝和傣族的相似，用葫芦和金竹管制成。吹管长6厘米，主管长50厘米 ，副管分别长33和17厘米 ，在两支副管紧贴主管的一侧，分别留有一条竹片，用线与主管捆扎在一起。在西盟佤族，用三根竹管插入葫芦底部，用蜡封固，上端和竹制吹管仅长两厘米。主管用于演奏旋律，两根副管发出纯五度音程的持续音为旋律伴奏。如不需要持续音或需要单持续音时，还可将副管堵住。'},
                  {title: '声色特点', content: '葫芦丝常用于吹奏山歌、小调等民间曲调，最适于演奏旋律流畅抒情的乐曲，在傣、阿昌等族人民中最为普及，是娱乐时助兴的乐器，在节日里，不论是在江中划龙舟或是在江边放 "高升"，还是在广场上"赶摆"或是在竹楼里饮酒欢宴，都可以听到其动听的声音青年男女传情达意或人们走在路上以及在田间劳动，也经常吹响葫芦丝，为人们的生活增添了许多欢乐。'},
                  {title: '代表作品',content: '《月光下的凤尾竹》、《情深意长》'}
                ],
                [
                  {title: '简介', content: '吐闰，又称"吐良"，一种吹孔气鸣乐器，主要流行于云南景颇族地区。'},
                  {title: '发展历史', content: ''},
                  {title: '制作流程', content: '用一根两端敞口的薄壁竹管制成，中间偏左处开一长方形吹孔，无按孔。民间常用者，管长约45厘米,管口内径约1厘米。演奏时左手食指、中指、无名指、小指反握管一端，以姆指控制左管口，右手用掌心开闭右管口，两手巧妙配合，以平吹和超吹技巧吹气发音。'},
                  {title: '声色特点', content: '音色高亢嘹亮，音域可达两个八度又纯五度。'},
                  {title: '代表作品',content: '《放牛调》、《山歌调》、《学山鸟叫》'}
                ],
                [
                  {title: '简介', content: '斯布斯额，是哈萨克族边棱气鸣乐器。也译作斯布孜额、斯比斯额。流行于新疆维吾尔自治区阿勒泰、布尔津、塔城、新源、乌苏、乌鲁木齐等地。'},
                  {title: '发展历史', content: '历史久远，成书于公元11世纪的古籍《突厥语大辞典》，其上载有"斯布孜额"为"一种箫"。可见，早在11世纪以前，斯布斯额就已出现并流传于民间了，而且世代相传 、流传至今。哈萨克族人民以畜牧为生，斯布斯额是老牧工经常吹奏的一种箫。'},
                  {title: '制作流程', content: '管身原用草原上或背阴山坳里生长的一种粗壮的芦苇制作，现多用杨木、松木或毡房的蓬杆制作。管身一般长50厘米、管径1.5厘米，管体中空，无簧哨装置。管身上开有三个或四个按音孔，管上端将外壁削薄作为吹口(图)，管外捆扎羊肠或细绳，以防气候干燥管身开裂，起保护作用。'},
                  {title: '声色特点', content: '斯布斯额是一种多泛音的吹管乐器，它常用于独奏，适宜远听，擅于演奏抒情性的乐曲。斯布斯额的音色飘逸、悠远，能模拟大雁等动物的鸣叫。'},
                  {title: '代表作品',content: '《思念》、《绑着的枣骝马》、《额尔齐斯河的波浪》、《赛驼》、《白色的走马》'}
                ],
                [
                  {title: '简介', content: '尺八，中国传统乐器，唐宋时期传入日本。竹制，内涂朱砂拌大漆填充（地）外切口，今为五孔（前四后一），属边棱振动气鸣吹管乐器，以管长一尺八寸而得名，其音色苍凉辽阔，又能表现出空灵、恬静的意境。'},
                  {title: '发展历史', content: '在中国的隋代和唐代时期，尺八成为宫廷中的主要乐器之一。而宋代开始，来自民间的箫、笛等乐器逐渐取代了用于宫廷乐的尺八的地位。尺八在1300多年前就已东传日本。据记载尺八是南北朝末年至唐初（公元581－618年），相当于日本的奈良时代作为演奏唐代的雅乐的乐器而由中国传去的。从隋高祖开皇20年（600年）起，日本圣德太子之时，日本曾屡派"遣隋使"和"遣唐使"前往中国，尺八和许多诸如龙笛等中国乐器东传日本。日本正仓院还保存有唐制尺八多种。最早传入日本的尺八仅用于宫廷雅乐，六孔，称为"古代尺八"，也常作为达官贵人的玩物，故也叫"雅乐尺八"。据传，日本圣德太子特别喜爱尺八，他当时所用的中国尺八，如今还珍藏在奈良的法隆寺中。'},
                  {title: '制作流程', content: '尺八和箫是否有联系，现已难以考证。尺八以竹根制作，中通无底，管体一尺八寸，歌口为外切半月形，正面开四个按孔，背面开一个按孔。尺八的制作非常复杂，要选择较粗，竹纤维紧密，内壁较厚的竹材，尺八的歌口为外切，而箫一般为内切，这是箫和尺八的区别之一，粗胚做好之后，现代尺八需要根据竹材条件进行内壁的填充，以实现内壁达到设计要求的曲线。'},
                  {title: '声色特点', content: '尺八的声音可高可低。音色可脆如银铃，也可细腻如丝。这就要求吹奏者有相当的把握能力。尺八的外切口歌口使得尺八与箫相比增加了沉浮技巧（通过抬头低头使得气流角度的改变产生的音高和音色也会改变）。古典尺八由于只有5个孔。使得有部分音必须配合沉浮才可吹出。所以沉浮是尺八的最大魅力之一。也是考证尺八演奏者水平的重要指标。'},
                  {title: '代表作品',content: '《虚铃》、《虚空》、《雾海篪》、《鹿之远音》、《鹤之巢笼》、《铃慕》、《一二三钵返》'}
                ],
                [
                  {title: '简介', content: '芒筒，是苗、侗、水、瑶等族单簧气鸣乐器，又称地筒、莽筒、芦笙筒。苗语称果董、董 果木，意为筒筒芦笙。侗语称筒卜、咚的，意为大竹筒。流行于贵州、广西、湖南等省区。民间芒筒依其大小组合在一起称芒筒队。大型芒筒队由十五或二十一支组成，分为大、中、小和最小芒筒四组。每组三至五支，与各种芦笙组成芦笙乐队。芒筒以圆润的持续音加强主音，使乐队音响丰满。'},
                  {title: '发展历史', content: ''},
                  {title: '制作流程', content: '管身竹制，由簧管和共鸣筒两部分组成(图)。簧管多采用一根细竹管制作，中间竹 节通 透，上端管口作为吹口，下端留节封闭，在底端开一长方形孔，镶嵌一枚铜制簧片而成，无 按音孔，可发出一个单音。也可用芦笙管作为簧管。共鸣筒用粗大毛竹筒制作，上端管口削 成45°斜坡形，中间竹节打通，筒底有开管和闭管两种形制：常见者底端留节封闭，为 闭管形制，另一种开管形制是在底端竹节以上，管身一侧挖削一个半圆形或三角形通孔。'},
                  {title: '声色特点', content: '芒筒发音浑厚、粗壮，弱奏音色圆润、柔和。 倍低音芒筒吹奏时，声如排山倒海，滚滚而来，其音数里之外可闻。'},
                  // {title: '代表作品',content: ''}
                ],
                [
                  {title: '简介', content: '布利亚，又称竹号。怒族边棱气鸣乐器。流行于云南省怒江傈僳族自治州碧江、兰坪和泸水等地。'},
                  {title: '发展历史', content: ''},
                  {title: '制作流程', content: ''},
                  {title: '声色特点', content: '演奏时，左手持共鸣筒，右手执吹管，将吹管敞口一端插入共鸣筒内，吹孔置于唇间吹奏。只发一音，其音为"嘟嘟"声，无固定音高。音色浑厚，音量较大，传播甚远。移动共鸣筒，可使竹号发出下滑音，其声更有悲伤之感。用于民间丧事活动，是怒族报丧时专用的吹奏乐器。'},
                  // {title: '代表作品',content: ''}
                ],
                [
                  {title: '简介', content: '筚达，是黎族单簧气鸣乐器，富有浓郁的地方特色。汉族称其为双管或黎族排箫。 流行于海南省东方市黎族支系。黎、美孚黎聚居地区和海南省专业艺术团体民族乐队中。'},
                  {title: '发展历史', content: ''},
                  {title: '制作流程', content: ''},
                  {title: '声色特点', content: '荜达的音色悠扬而高亢，能表现出壮族的激情与活力。在演奏时，荜达多与舞蹈配合，使音乐与动作相得益彰。荜达的音乐节奏明快，常常用于欢庆场合，如春节、丰收季节等。筚达音量较大，音色奔放，但较利列柔美、深沉。 往往由于两管的音高稍有差异，形成一种独特的音响效果，颇富山野风味。'},
                  // {title: '代表作品',content: '芦笙节：苗族芦笙节，舟溪芦笙节;著名乐曲：《诺德仲之歌》、《大悲调》、《和调》、《赛调》'}
                ]
              ],
            ds2:
              [
                [
                  {title: '简介', content: '琵琶，弹拨乐器首座，是中国传统弹拨乐器，已有两千多年的历史。最早被称为“琵琶”的乐器大约在中国秦朝出现。“琵琶”二字中的“珏”意为“二玉相碰，发出悦耳碰击声”，表示这是一种以弹碰琴弦的方式发声的乐器。中国近代汉族民族音乐史上有“海派”（浦东派）琵琶、“浙派”（平湖派）琵琶、“汪派”（汪派）等多流派。'},
                  {title:"发展历史",content: "大约在秦朝时期，一种圆形的长柄乐器开始流行，因其演奏方式而得名“批把”。后来，为了书写统一，改名为琵琶。汉代刘熙《释名·释乐器》中记载，琵琶本出于“胡中”，骑在马上演奏，向前弹奏称为“批”，向后弹奏称为“把”，因此得名“批把”。到了南北朝时期，从西域传来了一种梨形音箱、曲颈、四条弦的乐器，有人将其与中国的琵琶结合，改名为“曲项琵琶”。琵琶的演变过程体现了中亚文化交流对中华文化的深远影响。从北齐到唐代，曹氏琵琶家族在这一时期达到了巅峰，对盛唐时期的歌舞艺术产生了重要影响。经过不断发展，琵琶逐渐派生为：无锡派、平湖派、浦东派、崇明派、汪派"},
                  {title:"制作流程",content: "琵琶是由“头”与“身”构成，头部包括弦槽、弦轴、山口等。身部包括相位、品位、音箱、覆手等部分。琵琶的头部， 由“弦槽”、四只“轸子（弦轴）”、“山口”等组成。 身部的中下部分呈上狭下阔，底呈半圆，中空，即音箱； 品位粘在用梧桐板制成的“面板”上，四条弦系在下端“覆手”的四个小孔内，在覆手中央处的面板上，开有一个小孔， 称作“纳音”或“出音孔”。身部背面，称作“琵琶背”，背的上端与头相接，背的中下部与面板相粘接， 腹内另有二条横档和几个音柱，安置在一定的部位处。"},
                  {title:"声色特点",content: "琵琶在音色上,曾有“尖”“膛”“松”“脆”“爆”五种音色效果。“尖”是指在高音区的发音非常明亮;“堂”是指在低音区的发音特别洪亮;“松”是指在按弹发音时反应很灵敏,弹弦后的余音很强很长;“脆”是指发音很清脆;“爆”是指发音坚实而有分量。在一把好的琵琶上,就是要求这五种音色特点都能具备。不但在老缠弦的低音、中音区发音要洪亮,也要在子中弦的中音、高音区的发音很美好。"},
                  {title: '代表作品',content: '《夕阳箫鼓》、《月儿高》、《汉宫秋月》、《塞上曲》、《青莲乐府》、《飞花点翠》、《十面埋伏》、《霸王卸甲》、《海青拿天鹅》、《汉将军令》、《满将军令》、《阳春白雪》、《高山流水》、《春江花月夜》'}
                ],
                [
                  {title: '简介', content: '古筝，弹拨弦鸣乐器，又名汉筝、秦筝，是汉民族古老的民族乐器，流行于中国各地。经过千百年的发展，主要形成了客家筝、潮州筝、山东筝、河南筝四大流派。 常用于独奏、重奏、器乐合奏和歌舞、戏曲、曲艺的伴奏。因音域宽广，音色优美动听，演奏技巧丰富，表现力强，而被称为“众乐之王”，亦称为“东方钢琴”，是中国独特的、重要的民族乐器之一。'},
                  {title: '发展历史', content: '远在战国时代的秦国境内（陕西省），筝就已经流行，当时称为“秦筝”。此后，又流传到了河南、山东、广东、浙江、内蒙古等地。由于历史的变迁和地域的影响而造成了地方风格、演奏技法等的不同，并因此形成了当今各种不同的古筝流派，使古筝的发展日趋繁荣。筝在汉、晋以前设十二弦（见魏·阮璃《等筝赋》“弦有十二”）；隋唐时期，筝由十二弦增至十三弦；明清以后逐步增至十五弦；清末至民国初出现了新制的十六弦筝，这种十六弦第也就是中国近百年来流行的传统筝。古筝主要分为：陕西筝、河南筝、山东筝、潮州筝、客家筝、浙江筝、福建筝、朝鲜筝、日本筝。'},
                  {title: '制作流程', content: '筝是一种多弦多柱的弹拨乐器。它的外形近似于长箱形，中间稍微突起，底板呈平面或近似于平面。筝的头部有缓缓而落的筝脚。在木制箱体的面板上张设筝弦。在每条弦下面安置码子，码子可以左右移动，用来调整音高和音质。筝的品种不一，构造也不尽相同。主要由面板，底板、边板、筝头、筝尾、岳山、码子、琴钉、出音孔和筝弦等部位组成筝的共鸣体由面板、底板和两个筝边组成。在共鸣体内有音桥，呈拱形，它除了共鸣效果的需要外，还起着支撑的作用。共鸣体的质量和结构对筝的音响影响很大。'},
                  {title: '声色特点', content: '古筝的音色特点包括：高音清脆、明亮，中音圆润、饱满，低音浑厚、余音长，各弦音色过渡自然。演奏者可以通过控制手指的力度来实现对音色的把握，以达到理想的演奏效果。'},
                  {title: '代表作品',content: '《汉宫秋月》、《高山流水》、《梅花三弄》、《渔舟唱晚》'}
                ],
                [
                  {title: '简介', content: '扬琴是中国常用的一种击弦乐器，与钢琴同宗，音色具有鲜明的特点，音量宏大， 刚柔并济；慢奏时，音色如叮咚的山泉，快奏时音色又如潺潺流水。音色明亮，犹如大珠小珠落玉盘般清脆。表现力极为丰富，可以独奏、合奏或为琴书、说唱和戏曲伴奏，在民间器乐合奏和民族乐队中经常充当“钢琴伴奏”的角色，是一种不可缺少的主要乐器。扬琴传入中国以来，便迅速与中国的传统音乐表现形式相融合，成为中国音乐伴奏中不可缺少的重要组成部分，推动了民间说唱艺术的发展。扬琴的影响力也随着民间说唱艺术的发展被人们熟知和运用'},
                  {title:"发展历史",content: "扬琴在中国已经流传和演化了四百多年了。在这期间，中国的乐器制作家研制出了不少扬琴的新品种，比如像高低音扬琴、转调扬琴、筝扬琴和电声扬琴等。尤其值得一提的是电声扬琴，这种电声扬琴由琴体、拾音器、放大器和音箱组成，当琴弦振动的时候，声音波通过拾音器转变为电讯号，然后经过放大器美化音色之后，由扬声器发出。电声扬琴既能伴奏又能独奏，因此，从它问世以来，一直受到扬琴演奏家们的好评。除传统的八音、十音和十二音小扬琴适于民间器乐合奏或为说唱、戏曲伴奏使用外，变音扬琴、转盘转调扬琴、筝扬琴、全律活马大扬琴和电扬琴等则适于独奏或参加民族乐队合奏。"},
                  {title:"制作流程",content: "扬琴由共鸣箱、山口、弦钉、弦轴、马子、琴弦和琴竹等构成。共鸣箱是扬琴的形体，它由前后两块侧板和左右两端琴头连接成琴架，上下蒙以薄板而成。侧板和琴头使用色、烨木、榆木或其他质地较硬木材制作，琴架上的面板使用纹理顺直、均匀的梧桐木或鱼鳞松制作。它是音响的共鸣板，对扬琴的音量和音色起重要作用。琴架下面的底板，多使用三层的胶合板。"},
                  {title:"声色特点",content: "扬琴的音色丰富多彩，低音区发音朦胧、雄厚而深沉；中音区柔和、纯净而透明；高音区清脆、明亮；最高音区则较紧张。演奏旋律时主要用中音和高音区，有时也接触到最高音区，但很少使用。低音区较少演奏旋律，多用作和声的衬托。扬琴适于演奏快速的乐曲，最适合表现轻快、活泼的情绪和欢快、喜悦的感情。"},
                  {title: '代表作品',content: '《海峡音诗》、《沙洲掠影》、《冼星海曲》、《瞿春泉》、《唐招提寺》'}
                ],
                [
                  {title: '简介', content: '古琴，又称瑶琴、玉琴、七弦琴，是中国传统拨弦乐器，有三千年以上历史，属于八音中的丝。古琴音域宽广，音色深沉，余音悠远。古籍记载伏羲作琴，又有神农作琴、黄帝造琴、唐尧造琴等传说；舜定琴为五弦，文王增一弦，武王伐纣又增一弦为七弦；可见中华古琴文化的源远流长，博大精深。'},
                  {title:"发展历史",content: "据《史记》载，琴的出现不晚于尧舜时期。考古发现最早的古琴，为2016年在湖北枣阳郭家庙，出土的周朝曾国春秋早期的琴，距今2700年左右，将我国有实物佐证的琴史提前约300年。传说炎帝在沁源景凤羊头山下“黍定黄钟”，发现了音乐，炎帝神农氏又“削桐为琴、绳丝为弦”，创造了最初的琴。琴的型制于东汉晚期确定，马王堆3号汉墓出土的汉初七弦琴结构简单，音箱较小，共鸣声小，尾部为实木，面板无徽位。东汉至魏晋时期，琴在士人中非常流行。战乱和纷争的魏晋时期，士族阶层出现大量文人琴人，如“建安七子”和“竹林七贤”等，琴风盛行，他们不仅弹奏，而且创作大量琴曲。由于宋朝行使抑武扬文的政策，自宋太宗起，两宋期间自帝王至朝野上下都十分好琴，无不以能琴为荣，达到历代好琴的顶峰。宋元时期，琴开始出现明确的流派传承。第1个古琴流派浙派出现于南宋晚期，代表琴家有郭沔等。南宋姜夔作琴歌《古怨》，是现存最早的琴歌。民国时琴随文人出洋而流传到东南亚和欧美澳洲，尤其是在改革开放后大规模华人在海外的情况下，迄今在马来西亚、新加坡、美国、加拿大、德国、英国、荷兰和瑞典等都有琴师和琴社。"},
                  {title:"制作流程",content: "古琴一般长约三尺六寸五，象征一年三百六十五天；面圆底扁，象征天地；琴身与凤身相应（也可说与人身相应）有头、颈、肩、腰、尾、足。分别由琴面、琴弦、琴徽、琴头、轸子、轸池、护轸、鸭掌，绒扣及绒扣的搓捻系结和琴尾、琴底、龙池、凤沼、雁足、断纹组成。"},
                  {title:"声色特点",content: "古琴的声音是非常独特的，一般人听琴乐能感到古琴的安静悠远。“静”可以说是琴音的最大特点，琴音也被称为“太古之音”、“天地之音”。这里的“静”还有两层意义，一是抚琴需要安静的环境，二是抚琴更需安静的心境。古琴的三种音，散音、泛音、按音，都非常安静。散音松沉而旷远，让人起远古之思；其泛音则如天籁，有一种清冷入仙之感；按音则非常丰富，手指下的吟猱余韵、细微悠长，时如人语，可以对话，时如人心之绪，缥缈多变。泛音象天，按音如人，散音则同大地，称为天地人三籁。因此古琴一器具三籁，可以状人情之思，也可以达天地宇宙之理。"},
                  {title: '代表作品',content: '《潇湘水云》、《广陵散》、《流水》、《渔樵问答》、《平沙落雁》、《阳春白雪》、《阳关三叠》、《梅花三弄》'},
                ],
                [
                  {title: '简介', content: '柳琴，弹弦乐器。又称柳叶琴、金刚腿、土琵琶。中国传统乐器。原流行于苏、鲁、皖等省。现流行于全国各地，以山东临沂及苏北一带为主。 属唐代以来在民间流传的梨形音箱弹弦乐器之一种，其外形、构造、奏法均与阮相似。最早的柳琴，构造非常简单，由于柳琴的外形非常民间化，中国老百姓亲切地称它“土琵琶”。'},
                  {title: '发展历史', content: '柳琴原是流行于鲁、皖、苏一带的民间乐器，用作柳琴戏、泗州戏等地方戏曲的伴奏及弹奏简单歌曲，发音响亮宏大，音色高亢刚劲，富有浓郁的乡土气息。经过长时间的发展，现已发展为独奏乐器，并常用于民族乐队中的高音乐器。1958年底，第一代新型柳琴－－三弦柳琴诞生了；70年代，出现了第二代新型柳琴－－四弦高音柳琴。从而使柳琴结束了200余年来仅仅作为伴奏乐器的历史，走上了独奏乐器的发展道路。'},
                  {title: '制作流程', content: ''},
                  {title: '声色特点', content: '在民乐队中，柳琴是弹拨乐器的高音乐器，其音响效果独特，不易被其它乐器所掩盖，常用来演奏高音区的主旋律，有时也演奏华丽、技巧性高的华彩乐段。它既适于演奏欢快、对比强烈、节奏鲜明、富有弹性和活泼的曲调，也适于演奏优美、抒情的旋律，在乐队中能与琵琶、阮、筝和二胡等乐器和谐默契地合作。柳琴还具有西洋乐器曼陀林的音响效果，它与西洋乐队合作时更别有风味，在音色上也能水乳交融、相得益彰。'},
                  // {title: '代表作品',content: ''}
                ],
                [
                  {title: '简介', content: '月琴，中国传统弹拨乐器，起源于汉代，随着时间的推移，月琴也在不断变化发展中。比如，月琴传到了台湾以后，形状有了一些小变化。台湾月琴的颈较长，音箱为木制圆形，偶有6角形或8角形。早期用蚕丝弦，现常用尼龙弦，颇流行使用钓线。是台湾歌仔戏、歌仔说唱。闽南的歌仔戏（芗剧）和歌仔（当地称锦歌，民谣的总称）说唱亦使用台湾月琴。'},
                  {title: '发展历史', content: '月琴源于阮。早在汉武帝时（公元前140年—前87年），中国人已经创造发明了阮，但当时这种以“弹”和“挑”为主要演奏手法的乐器，都笼统地称为琵琶。《旧唐书·音乐志》：“阮咸，亦秦琵琶也，而项长过于今制，列十有三柱。武太后时，蜀人蒯朗于古墓中得之，晋《竹林七贤图》所弹如此类，因谓之阮咸。咸，晋世实以善琵琶知音律称。”月琴是由阮演变而来的。到了清代，月琴就与阮完全不同了，琴杆缩短为琴颈，演变为现代的式样。'},
                  {title: '制作流程', content: '月琴结构和阮近似，不同处是在琴身中胶有音梁和支有音柱、琴颈短小。月琴由琴头、琴颈、琴身、弦轴、琴弦和缚弦等部分组成。琴头和琴颈是用一整块紫檀或其他硬木制作，琴身是呈扁圆形的共鸣箱，由面、背、框板胶粘而成；弦轴用黄杨木，外表刻有斜条瓣纹，拧转的一端还嵌有象牙或骨饰；琴弦用丝弦、尼龙弦或尼龙缠钢丝弦，缚弦用红木或竹子制作，粘在面板下部的中间。'},
                  {title: '声色特点', content: '月琴的音色悦耳、动听，高音清脆、中音明亮、低音丰满。用拨片弹奏的月琴，右手奏法以弹、挑、滚为主，有时也用撮、扫滚等，并有独特效果的“刮品”技巧。左手则有按、颤、滑、吟等技法。'},
                  {title: '代表作品',content: '《刮地风》、《野马过河》、《四季春》、《敉西调》、《春天》、《百万雄师过大江》'}
                ],
                [
                  {title: '简介', content: '秦琴，又称梅花琴，中国传统弹弦乐器，由古时的“弦鼗”发展而来，结构和阮相似。音箱由六或八块硬质木板胶接成边框，呈梅花形、圆形、六方或八方形，两面蒙桐木薄板。琴杆窄而长，用硬木制作，上嵌十九品，按十二平均律排列。琴头雕有梅花、如意、蝙蝠或铲头为饰。全长89、音箱直径28厘米。'},
                  {title: '发展历史', content: ''},
                  {title: '制作流程', content: ''},
                  {title: '声色特点', content: '明亮、柔和，与别的乐器合奏，音响协调，是结合高、低音乐器的中音乐器。现多用于广东音乐、潮州大锣鼓和民族乐队，也作为各地方剧种的伴奏乐器。'},
                  {title: '代表作品',content: '《广陵散》'}
                ],
                [
                  {title: '简介', content: '阮[ruǎn]是一种汉族传统乐器，阮咸的简称。相传西晋阮咸善弹此乐器，因而得名，四弦有柱。始于唐代，元代时在民间广泛流传，成为人们喜爱的弹拨乐器，有了广阔的音域和丰富的表现力。还有一意即长颈琵琶，与从龟兹传来的曲项琵琶不同。西晋竹林七贤之一阮咸善弹琵琶，唐代开元年间从阮咸墓中出土铜制琵琶一件，命名为“阮咸”，简称“阮”。'},
                  {title: '发展历史', content: '阮咸，亦秦琵琶也，而项长过于今制，列十有三柱。武太后时，蜀人蒯朗于古墓中得之，晋《竹林七贤图》所弹如此类，因谓之阮咸。咸，晋世实以善琵琶知音律称。“月琴是由阮演变而来的。到了清代，月琴就与阮完全不同了，琴杆缩短为琴颈，演变为现代的式样。'},
                  {title: '制作流程', content: '阮由琴头、琴颈、琴身、弦轴、山口、琴品、琴码、缚弦和琴弦等部分组成。琴头和琴颈是用两块硬质木料胶合而成，琴头顶端多饰以民族风格的雕刻，琴颈上粘有指板；琴身是一个呈扁圆形的共鸣箱，由面板、背板和框板胶合而成'},
                  {title: '声色特点', content: '小阮是高音乐器，发音清脆、明亮，在乐队中常担任演奏曲调；以加强中阮效果，流畅的琶音最适宜映衬抒情的旋律，演奏单音或和弦时，，能加强节奏、烘托出乐曲热烈奔放的效果。中阮和大阮，既适于独奏，也适于合奏或伴奏。在民族乐队中，常用来组成双音或三音和弦等各种音型，以加强整个乐队的演奏效果。'},
                  {title: '代表作品',content: '《丝路驼铃》、《云南回忆》、《幽远的思念》、《天高云淡》'}
                ],
                [
                  {title: '简介', content: '热瓦普，又称热瓦甫、拉瓦波、喇巴卜。维吾尔族，乌孜别克族弹弦乐器。流行于中国新疆维吾尔自治区。相传创制于十四世纪。毛拉·艾斯木吐拉穆吉孜《乐师史》中记载，热瓦普起源于南疆喀什。民间流传的喀什热瓦普，全长130厘米。流行于新疆维吾尔自治区天山南北。相传创制于十四世纪。清代被列入宫廷的回部乐。《律吕正义后编》（公元1746年成书）收录称喇巴卜。'},
                  {title: '发展历史', content: '公元14世纪，国内外民族文化交流广泛开展，维吾尔族人民在原有民间乐器的基础上，吸收外来乐器，创制出一些新乐器，喀什热瓦普就是维吾尔族人民勤劳、智慧的结晶，在民间一直盛传不衰。到了清代，热瓦普被列入宫廷回部乐（即维吾尔族音乐）中，并以喇巴卜、喇叭卜之名载入史册。'},
                  {title: '制作流程', content: '民间流传的喀什热瓦普，全长130厘米。音箱呈半球形，用整块桑木挖制，面部蒙羊皮或驴皮。琴头的弦槽部位向后呈直角弯曲。琴杆上缠有二十八个丝弦品位。琴杆与音箱连接处两侧各设一弯角。琴杆、音箱上镶有驼骨、贝壳等装饰。热瓦普分共有以下几种类型：阿勒拉热瓦普、牧羊人热瓦普、且末热瓦普、安迪尔热瓦普、朗诺热瓦普、多朗热瓦普、阿瓦提热瓦普、哈密热瓦普、巴楚热瓦普等'},
                  {title: '声色特点', content: '演奏以原把位为主，高音旋律常在共鸣弦上弹奏。主奏弦音色深沉、浑厚，共鸣弦音色清脆、明亮。两者交织在一起，构成音响铿锵、豪迈奔放的独特风格。可用于独奏、合奏或伴奏，也可自弹自唱。是民间演奏“多朗木卡姆”的主要伴奏乐器之一。'},
                  {title: '代表作品',content: '《林派特》、《亚鲁》、《宫特帕依》、《塔什瓦依》、《我的热瓦普》、《夏地亚那》'}
                ],
                [
                  {title: '简介', content: '冬不拉又名东不拉、东布拉，是中亚地区的传统弹拨乐器，在哈萨克斯坦、吉尔吉斯斯坦以及中国新疆的哈萨克族中尤其流行。冬不拉作为一种文化的象征，反映着中国历史的不断发展，以及哈萨克民族的情感表达方式。可以说早期的冬不拉是各个部落之间表达情感的重要纽带；在当代，冬不拉则是维系中华民族文化的纽带。'},
                  {title: '发展历史', content: '冬不拉的历史悠久早在于《史记》和《汉书》中便有对于“乌孙”的记载。据中国音乐专家以及历史学家推测，这里所说的“乌孙”便是冬不拉。在时代发展的长河中，冬不拉早在公元三世纪就已经在哈萨克族开始流传。冬不拉在历史的不断发展之下，也在不断的进步，随着时代的不断发展，哈萨克民族音乐家们对冬不拉进行了改造，不仅将冬不拉的琴弦换成了钢丝琴弦，而且冬不拉的品位也在不断的增加，使得冬不拉的延音更加绵长、音域更加宽广。'},
                  {title: '制作流程', content: '冬不拉的制作工艺基本可分为六个程序，分别是：选择材料、量尺裁切、加工工艺、装饰手法、上漆晾干、装弦调音。'},
                  {title: '声色特点', content: '冬不拉在历史的不断发展之下，也在不断的进步，随着时代的不断发展，哈萨克民族音乐家们对冬不拉进行了改造，不仅将冬不拉的琴弦换成了钢丝琴弦，而且冬不拉的品位也在不断的增加，使得冬不拉的延音更加绵长、音域更加宽广。'},
                  {title: '代表作品',content: '《高潮》、《热爱祖国》、《延安颂》、《伊犁河的波浪》、《我的冬不拉》、《白色的小岛》、《故乡》、《姑娘的温柔》'}
                ],
                [
                  {title: '简介', content: '三弦琴，又称三弦，中国传统弹拨乐器，亦传入琉球、日本等地。'},
                  {title: '发展历史',content: '三弦琴，又称三弦，中国传统弹拨乐器，亦传入琉球、日本等地。三弦起源何时尚无定论，据传源自秦汉时期的弦鼗，而“三弦”之名最早见于唐代崔令钦的《教坊记》，但文中未详细记述“三弦”形制。三弦兴盛于元代，是元曲的主要伴奏乐器之一，明代杨慎认为“今次三弦，始于元时”，而清代毛其龄认为“三弦起于秦时，本三十鼓鼓之制而改形易响，谓之鼓鼓，唐时乐人多习之，世以为胡乐，非也”。随着四川广元南宋墓伎乐石雕和河南焦作金墓三弦乐俑的出土，可知三弦早于元代就已出现。'},
                  {title:"制作流程",content: "三根弦，故名“三弦”。分琴头，琴颈，琴身。琴颈为指板，比较长。琴身两面蒙以蛇皮。1920年代出现加弦三弦，并未普及。北方的三弦一般长122厘米，称为“大三弦”，南方三弦一般长95厘米，称为“小三弦”。琴头上端为锄头状，或饰以象牙片或骨片，下开弦槽及弦轴孔，左边装2弦轴，右边装1弦轴；琴颈上接琴头，下接琴身，琴颈上端为山口，琴颈几乎全为无品指板，指板与皮膜在同一水平面，琴颈下端一段向背弯曲与连接琴身；琴身又称琴鼓，略程方形，以原木凿空，或以木片胶合成木框，两面蒙以蛇皮。"},
                  {title: '声色特点',content: '三弦的音色干涩，音量大。20世纪50年代至20世纪60年代经肖剑声的改革，音色变为厚亮，大型的三弦音域多于3个八度。用于地方音乐，亦用于交响化的民族乐团，也有大量的独奏曲和合奏曲。'},
                  {title: '代表作品',content: '《西河词话》'}
                ],
                [
                  {title: '简介', content: '弹波尔，维吾尔族、乌孜别克族古老的弹拨弦鸣乐器。史籍曾译称弹拨尔、丹布尔、丹不尔。流行于新疆维吾尔自治区各地。弹拨乐器的一种。指板较长，有十六个品位，两根铜弦分两组定音，ggd1gg或d1d1gd1d1，音域大三个八度。两根外弦为主奏弦，两根内弦为伴奏弦，用于独奏、伴奏与合奏。'},
                  {title: '发展历史', content: '弹布尔的历史较为悠久，相传在十四五世纪时，已有演奏弹布尔的著名民间艺人。清代列入宫廷回部乐，清《钦定大清会典》（卷四十二）载：“丹不尔刻桐为体，以大匏为槽，……直柄，面平背圆，……铁弦四，上二轴，左右各一绾之。……柄上设二铁为山口，近上一片穿孔纳弦，下一片承弦，柱用角。”这一记载与今日广泛流传的弹布尔略有不同。清代《新疆图志》（卷四十八）也载有：“铜弦谓之弹普”。弹普即弹布尔。这种乐器最初流行于南疆，正式记载见于1854年毛拉·艾斯木吐拉穆吉孜撰写的《乐师史》（古维文手抄本）中。'},
                  {title: '制作流程', content: '弹布尔的外形，像一个安了长柄的葫芦水瓢。它由共鸣箱、琴头、琴杆、弦轴、马子和琴弦等部分组成。用桑木、核桃木制作。共鸣箱呈半梨形，蒙以桐木或白松薄板为面。琴杆较细而长，顶端为琴头，平顶无饰、呈方柱形，不设弦槽，上置T形弦轴。琴杆表面为按弦指板，上缠弦品。张金属弦。弹布尔因流传地域的不同，而有南疆弹布尔和北疆弹布尔之分。'},
                  {title: '声色特点', content: '历史悠久，形制古朴，音色优美，富有独特的地方风格，常用于独奏。 汉文史籍中，有“丹不尔”、“弹拨尔”和“丹布尔”等译名。 琴身木制，音箱较小，成瓢形。 琴身修长，以丝弦缠成16至20余品位。 有钢丝弦五根，内二弦与外二弦调成同音，与中弦成五度关系。 音域宽广，音色清脆，富有浓郁的民族风格。'},
                  {title: '代表作品',content: '《艾介姆》、《林派特》、《三只雁子》、《沙巴》、《祖国是花园》、《给母亲的歌》'}
                ],
                [
                  {title: '简介', content: '口弦是一种古老的簧片乐器，主要流传于中国四川、云南和贵州一带的少数民族地区。口弦是一种古老且外形较小的民间乐器，有其独特的音阶，曾流行于中国部分少数民族地区。口弦表演用于当地的婚丧礼俗与民俗节日中，其演奏音乐来自民歌、小调乃至即兴诗歌，演奏内容包括宗族历史、古代传说、日常劳作等。中国近20多个民族有口弦这件乐器，各民族有自己不同的独特称谓，在具体形态上也有铜制、铁制、竹制、单片、多片等不同。'},
                  {title: '发展历史', content: '在中国原始社会母系氏族时期，就有了口弦。当时名“簧”，簧在古代与竽、笙、篪等乐器相提并论，可见其重要。元代以后，簧在中原内地逐渐失传。簧的名称也渐渐被人们遗忘了。明代以来，口弦以口琴之名见于史册。清乾隆动撰的《清朝通典》在谈到“口琴”时说：“以铁为之，一柄两股，中设一簧，末出股外。横衔于口，鼓簧转舌，嘘吸成音。”《大清会典图》也有详细记述，并载有其图。'},
                  {title: '制作流程', content: '竹制口弦选用质坚干透的竹片，必须用滚开的清油把竹子炸透，去掉原竹子的青气，使之成为熟竹片，再削制而成。制作时，采取坚硬的楠竹片，用锋利刀子刳薄竹片，并在中间三面镂空刻出簧舌。竹口弦的簧舌和风琴的簧舌相似，簧舌的首部宽大而薄，朝向柄端，中部窄而厚，舌的两侧削成斜面，簧舌的根部较薄，与片头处相连，弹动片头时，利用竹片的弹性，可使簧舌自由往复振动发音，音响低沉，音色优美动听，这是竹制单片弦。如将多个单片弦的柄端钻孔穿绳，就成为竹制的多片弦。'},
                  {title: '声色特点', content: '竹制的音色深厚、低沉；铜制的音色清脆、秀丽。吹奏口弦随着口形、气力变化音色，形成铮铮动听，柔和婉转、非常接近口语的音乐旋律，利用口腔共鸣吹奏出不同的声调。 从而借以传递心声。 '},
                  {title: '代表作品',content: '《廊檐滴水》、《月影愁眠》'}
                ],
                [
                  {title: '简介', content: '箜篌（kōng hóu），又称坎侯或空侯，是中国传统拨弦乐器。在古代除宫廷乐使用外，在民间也流传。在古代有卧箜篌、竖箜篌、凤首箜篌三种形制，琴弦一般系在敞开的框架上，用手指拨弹。'},
                  {title: '发展历史', content: '据考证，箜篌流传至今已有两千多年的历史了。箜篌在古代除宫廷乐队使用外，在民间也广泛流传。在中国盛唐（618-907）时期，随着经济文化的飞速发展，箜篌演奏艺术也达到了相当高的水平，也就是在这个时期，中国古代的箜篌先后传入日本、朝鲜等邻国。古代箜篌可粗分为竖箜篌和卧箜篌，其中早期卧箜篌出如今春秋时的楚国，与古琴同一起源，但其长形共鸣箱面板上却有像琵琶一样的品位，这是它与琴瑟在形制上的主要差异；竖箜篌却是由远古狩猎者的弓演变而来，是伴随着人类最早文明的诞生而诞生的最古老的弦鸣乐器，有着五千年以上的历史。'},
                  {title: '制作流程', content: '现代箜篌有两排弦，每排36根，共72根。每根弦都通过一个雁柱（类似于古筝的琴码）连接在共鸣箱上。雁柱可以调节弦高和音高，并可以实现转调功能。现代箜篌的共鸣箱是梨形的（参考琵琶的共鸣箱），位于两面弦的中间。现代箜篌在琴柱顶端雕刻有凤首，作为中国传统文化的象征。箜篌的种类有：卧箜篌、竖箜篌、凤首箜篌、小箜篌、双排弦箜篌、雁柱箜篌、转调箜篌。'},
                  {title: '声色特点', content: '箜篌的音色更清越空灵，更鲜明一些，冷冷似雪山清泉之声。箜篌的低音更为醇厚悠远，高音更为清脆。箜篌的音色也有些类似于古筝，只不过是不戴指甲的古筝的音色罢了。'},
                  {title: '代表作品',content: '《高山流水》、《湘妃竹》、《渔舟唱晚》、《月儿高》、《文心雕龙》'}
                ],
                [
                  {title: '简介', content: '扎木聂，又称扎年。藏族传统弹弦乐器。因其设六弦，故又称六弦琴。流行于西藏自治区、四川、云南、青海、甘肃等地。据藏文史书《红史》载，扎木聂于唐代从内地传入。'},
                  {title: '发展历史', content: '札木聂于唐代从内地传入西藏，从古代壁画上，可见到既有用手拔弹的札木聂，也有用弓拉奏的札木聂。'},
                  {title: '制作流程', content: '传统的扎木聂，由共鸣箱、琴头、琴杆、弦轴、马子和琴弦等部分组成，在藏族民间这些部分还有其俗称，用红木、核桃木或檀香木制，全长108厘米。音箱为半葫芦形，长29厘米，上部较小呈棱形，下部较大呈椭圆形，蒙山羊皮或獐子皮。琴杆细长，指板无品。琴头向后半弧形弯曲，六轴分列两侧，顶端雕刻成龙头或无饰。'},
                  {title: '声色特点', content: '藏语“扎”是声音，“聂”为悦耳好听之意，“扎木聂”意为悦耳的琴声，扎木聂为四度关系音域琴，有三弦琴、四弦琴、六弦琴和八弦琴等种类。 其中以六弦琴最为普遍。 形式同二胡相似，音色细尖、悠远，音量较小。 '},
                  {title: '代表作品',content: '《阿妈勒火》、《达娃雄奴》、《耶几长木》'}
                ]
              ],
            ds3:
              [
                [
                  {title: '简介', content: '堂鼓是一种中国传统打击乐器，广泛用于戏曲、民乐和仪式音乐中，声音洪亮，具有很强的节奏感。'},
                  {title: '发展历史', content: '堂鼓起源悠久，早在商周时期就有类似的鼓类乐器。其形制在历代不断演变，并广泛用于宫廷、民间和宗教音乐。'},
                  {title: '制作流程', content: '鼓身通常由木材制成，鼓面采用牛皮或羊皮，经过张紧处理。两端用绳索或金属环固定，以确保鼓皮的紧绷度。'},
                  {title: '声色特点', content: '音色洪亮、浑厚，具有较强的穿透力，可表现强烈的节奏感。'},
                  {title: '代表作品',content: '京剧锣鼓点《夜深沉》、民族鼓乐《龙腾虎跃》'}
                ],
                [
                  {title: '简介', content: '铜鼓是一种古老的青铜乐器，在中国南方多个民族中广泛使用，兼具礼仪、乐器和财富象征功能。'},
                  {title: '发展历史', content: '铜鼓的历史可追溯到商周时期，在秦汉时期广泛流行，主要见于壮族、侗族、苗族等少数民族文化中。'},
                  {title: '制作流程', content: '采用青铜浇铸工艺，鼓面上通常刻有精美的花纹，具有浓厚的民族特色。'},
                  {title: '声色特点', content: '声音深沉、浑厚，可用于传递信号或节奏伴奏。'},
                  {title: '代表作品',content: '铜鼓舞、少数民族庆典音乐'}
                ],
                [
                  {title: '简介', content: '碰铃是一种金属打击乐器，常用于戏曲、庙会和仪式音乐中，具有清脆悦耳的声音。'},
                  {title: '发展历史', content: '碰铃历史悠久，早期用于佛教仪式和戏曲伴奏，现广泛应用于民族乐队。'},
                  {title: '制作流程', content: '由铜或黄铜制成，通常成对使用，用手或木槌敲击发声。'},
                  {title: '声色特点', content: '声音清脆明亮，适用于强调节奏或制造氛围。'},
                  {title: '代表作品',content: '戏曲伴奏、民乐合奏'}
                ],
                [
                  {title: '简介', content: '长鼓是朝鲜族传统打击乐器，常用于舞蹈和节庆表演，具有独特的音色和演奏方式。'},
                  {title: '发展历史', content: '长鼓起源于古代朝鲜半岛，随朝鲜族迁徙传入中国，广泛应用于民间庆典和宗教仪式。'},
                  {title: '制作流程', content: '鼓身为木制，鼓面用牛皮或羊皮制成，两端分别固定，用鼓棒或手掌击打演奏。'},
                  {title: '声色特点', content: '音色多变，可通过不同的击打方式发出深沉或清脆的声音，富有表现力。'},
                  {title: '代表作品',content: '朝鲜族舞蹈《长鼓舞》'}
                ],
                [
                  {title: '简介', content: '京堂鼓是中国戏曲音乐中的重要打击乐器，广泛用于京剧、评剧等戏曲伴奏。'},
                  {title: '发展历史', content: '京堂鼓源于传统戏曲乐队，常与锣鼓等乐器配合使用，以增强戏曲表演的节奏感。'},
                  {title: '制作流程', content: '鼓身以木材制成，鼓面蒙以牛皮或羊皮，鼓框采用金属或木质结构以固定鼓皮。'},
                  {title: '声色特点', content: '音色明亮，节奏感强，可用于突出戏曲音乐的戏剧效果。'},
                  {title: '代表作品',content: '京剧锣鼓经'}
                ],
                [
                  {title: '简介', content: '排鼓是一组不同音高的小鼓组合，常用于现代民族管弦乐队和打击乐合奏。'},
                  {title: '发展历史', content: '排鼓的形式起源于西方鼓组，但在中国现代民族乐队中得到了发展，成为民族打击乐的重要组成部分。'},
                  {title: '制作流程', content: '鼓身以木材制成，每个鼓面蒙以动物皮革或合成膜，鼓的大小不同，以产生不同音高。'},
                  {title: '声色特点', content: '音色清脆明亮，富有层次，可用于复杂的节奏表现。'},
                  {title: '代表作品',content: '民族乐团打击乐合奏'}
                ],
                [
                  {title: '简介', content: '傣族标志性乐器，形似象脚，用于节庆与舞蹈伴奏'},
                  {title: '发展历史', content: '源自傣族原始宗教祭祀，与泼水节、孔雀舞紧密关联，2024年作为《跃动鼓韵》核心元素亮相全国少数民族运动会'},
                  {title: '制作流程', content: '芒果树干掏空，上端蒙牛皮，鼓身彩绘金漆图案，鼓尾缀孔雀翎毛'},
                  {title: '声色特点', content: '低音深沉如象鸣，高音清脆似蹄踏，通过手掌拍击与手指弹奏变化音色'},
                  {title: '代表作品',content: '《孔雀舞》伴奏曲、《傣家欢歌》'}
                ],
                [
                  {title: '简介', content: '汉族传统变音打击乐器，由多面音高不同的小锣组合而成'},
                  {title: '发展历史', content: '唐代出现雏形，宋代纳入宫廷雅乐，现代多用于大型民族管弦乐团'},
                  {title: '制作流程', content: '十至二十四面铜制小锣按音阶排列，悬挂于木架，每锣由三绳固定'},
                  {title: '声色特点', content: '音色清亮如泉，余韵悠长，擅长表现空灵意境'},
                  {title: '代表作品',content: '《万载得胜鼓》配乐、《春节序曲》民族乐团版'}
                ],
                [
                  {title: '简介', content: '维吾尔族单面手鼓，伊斯兰文化圈重要乐器'},
                  {title: '发展历史', content: '沿丝绸之路传入新疆，与木卡姆音乐共生发展'},
                  {title: '制作流程', content: '木制圆框蒙羊皮，框内嵌铁环增强共鸣'},
                  {title: '声色特点', content: '掌击中心闷响如雷，边缘弹指清脆似铃'},
                  {title: '代表作品',content: '《十二木卡姆》、刀郎麦西热甫舞曲'}
                ],
                [
                  {title: '简介', content: '中国古代青铜乐钟编组，礼乐制度象征'},
                  {title: '发展历史', content: '起源于中国新石器时代晚期，距今已有四千多年的历史。最早的编钟发现于河南省偃师市的二里头文化遗址，属于夏朝时期。商朝和西周时期，编钟逐渐发展完善，成为宫廷雅乐的重要组成部分。春秋战国时期，编钟达到了鼎盛时期，不仅用于宫廷礼仪，还广泛流传于民间。编钟由大小不同的铜钟组成，悬挂在木架上，形成一套完整的音乐系统。编钟的构造分为钟体、钟顶、钟裙三部分'},
                  {title: '制作流程', content: '铸造、雕刻、调音等多种工艺。工匠选用优质的青铜原料，经过多次熔炼、浇铸、打磨等工序，制作出形态美观、音质优良的编钟。'},
                  {title: '声色特点', content: '纯净、悦耳，声音宏亮而富有共鸣力,通常由一套12次取自全音阶的大字编钟或半音阶的小字编钟组成，总共有2个至5个八度'},
                  {title: '代表作品',content: '曾侯乙编钟出土乐曲复原'}
                ],
                [
                  {title: '简介', content: '中国古代南方少数民族代表性青铜打击乐器，兼具乐器和礼器功能，被称为\'北鼎南鼓\''},
                  {title: '发展历史', content: '起源于春秋战国，由铜釜演化而来，汉代广泛用于战争与祭祀，现主要留存于广西河池红水河流域，被誉为\'世界铜鼓之乡\'（现存1400余面）'},
                  {title: '制作流程', content: '青铜铸造，鼓面饰太阳纹、蛙纹等图腾，鼓身分胸、腰、足三部分，音高由鼓面直径与厚度决定'},
                  {title: '声色特点', content: '低沉浑厚，穿透力强，公鼓（高音）与母鼓（低音）配合可形成和声'},
                  {title: '代表作品',content: '壮族‘蚂拐节’铜鼓舞、瑶族‘祝著节’祭鼓仪式、南丹白裤瑶丧葬铜鼓乐'}
                ],
                [
                  {title: '简介', content: '对一种戏曲声腔系统的总称，以硬木梆子击节为特色而得名'},
                  {title: '发展历史', content: '起源于西周时期，源于西府地区，即陕西省宝鸡市的岐山（西岐）与凤翔（雍城）'},
                  {title: '制作流程', content: '梆子由两根长短不等、粗细不同的实心硬木棒组成。长25厘米的一根为圆柱形，直径4厘米，另一根短而粗的为长方形，长20、宽5-6、厚4厘米'},
                  {title: '声色特点', content: ''},
                  {title: '代表作品',content: '《窦娥冤》'}
                ],
                [
                  {title: '简介', content: '藏传佛教密宗的重要法器，也是藏族传统摇击体鸣乐器。其形制为铜制平口钟形，内悬金属铃舌，铃身装饰精美花纹，柄部多铸有文殊菩萨头像或金刚杵造型。法铃常用于宗教仪式、诵经法事及佛乐演奏，象征智慧与慈悲，与金刚杵成对使用，寓意 "方便与智慧双运"'},
                  {title: '发展历史', content: '法铃起源于古印度，作为密宗法器随佛教传入中国，约在唐代前后进入西藏地区。'},
                  {title: '制作流程', content: '主体采用响铜（铜锡合金）铸造，铃柄多为铜或银制，部分高端法铃镶嵌珊瑚、宝石'},
                  {title: '声色特点', content: '摇击时音色清脆通透，余音绵长，穿透力强，适合宗教仪式中的冥想与诵经氛围'},
                  {title: '代表作品',content: '《冈仁波齐》'}
                ],
                [
                  {title: '简介', content: '又称南堂鼓、缸鼓，是中国汉族传统打击乐器，因鼓身上宽下窄、形似花盆而得名。鼓身木制，两面蒙以牛皮，演奏时悬于特制鼓架，以双木槌敲击。其音色低沉雄浑，常用于国乐合奏、戏曲武场伴奏及民俗活动，尤其在表现战争场面或营造紧张氛围时效果显著'},
                  {title: '发展历史', content: '花盆鼓雏形可追溯至西夏时期（1038–1227 年），宁夏东千佛洞西夏石窟壁画中已出现类似形制的鼓类乐器，反映其早期作为军乐或宗教仪式乐器的功能,明清两代，花盆鼓形制逐渐完善，鼓身雕刻云龙图案，成为宫廷与民间广泛使用的乐器，尤其在京剧、京韵大鼓等戏曲伴奏中占据重要地位,至今，花盆鼓仍活跃于传统音乐舞台，部分地区将其融入民俗活动，如山西襄汾的 "花腔鼓" 表演，保留了古老的鼓乐风格'},
                  {title: '制作流程', content: '鼓身选用椿、杨或柳木，经车旋成上宽下窄的鼓腔（高约 60 厘米，面径 57 厘米，底径 28.5 厘米）；鼓皮以水牛皮或旱牛皮蒙制，上鼓皮优选牛脊背皮以保证音质。,鼓身涂深棕色漆，彩绘金色云龙纹或吉祥图案，鼓架多为四柱雕花结构，兼具实用性与艺术性'},
                  {title: '声色特点', content: '鼓面较大，蒙皮较厚，敲击时音色饱满低沉，余音绵长，适合表现庄重或激烈的场景。'},
                  {title: '代表作品',content: '《击鼓骂曹》、《梁红玉擂鼓战金山》、《夜深沉》'}
                ],
                [
                  {title: '简介', content: '又称 "洋琴""铜丝琴""蝴蝶琴" 等，是中国传统击弦乐器。其琴体呈扁梯形或蝴蝶形，面板张设钢丝弦，通过竹制琴竹敲击发声。扬琴音域宽广、音色灵动，兼具独奏、伴奏与合奏功能，广泛应用于戏曲、说唱及民族管弦乐中，是广东音乐、江南丝竹等乐种的核心乐器，被誉为 "中国钢琴"'},
                  {title: '发展历史', content: '扬琴起源于古波斯（今伊朗），约明朝（1368–1644 年）经海路传入中国广东，初称 "洋琴"，后经本土化改造，成为独立乐器,清初主要用于戏曲伴奏（如粤剧、闽剧），清末随民间器乐合奏兴起，流传至全国，形成广东、江南、四川、东北四大流派,20 世纪后，扬琴形制不断改良，出现 401 型、402 型等现代扬琴，音域扩展至四个半八度，表现力进一步提升。'},
                  {title: '制作流程', content: '琴体框架多用桦木、榆木，面板以鱼鳞松或桐木制成，琴弦为钢丝（高音）或缠弦（低音）'},
                  {title: '声色特点', content: '高音区如金玉交鸣，中音区温润流畅，低音区浑厚深沉'},
                  {title: '代表作品',content: '《将军令》、《旱天雷》、《雨打芭蕉》'}
                ],
                [
                  {title: '简介', content: '朝鲜族农乐舞核心乐器，腰鼓形制'},
                  {title: '发展历史', content: '起源于印度 细腰鼓，又名"杖鼓 "，历史曾流行于中原地区'},
                  {title: '制作流程', content: '古代的长鼓，用獐皮做左边的厚鼓皮，用狗皮做右边的薄鼓皮，鼓筒采用木材或多层纸、薄铁等材料制成'},
                  {title: '声色特点', content: '左手击低音鼓面，右手执竹鞭敲高音，节奏复杂'},
                  {title: '代表作品',content: '《珍岛阿里郎》'}
                ],
                [
                  {title: '简介', content: '维吾尔族铁腔皮鼓，常用于喜庆场合'},
                  {title: '发展历史', content: '纳格拉鼓即“铁鼓”，在清代史籍中称“哪噶喇”、“奴古拉”，因鼓身用铁铸成，故称“铁鼓”,又有“冬巴”之称。据说古代人们把它系在马鞍上当作战鼓。大约在9世纪以后,它才成为维吾尔族的打击乐器。'},
                  {title: '制作流程', content: '骆驼皮要在水里泡十几天，把毛拔干净后，再蒙在铸铁做的鼓身上，用绳子系紧，直到骆驼皮变干'},
                  {title: '声色特点', content: '铸铁鼓身蒙骆驼皮，双鼓一组音高互衬'},
                  {title: '代表作品',content: '《纳格拉 (Live)》'}
                ],
               
              ],
            ds4:
              [
                [
                  {title: '简介', content: '又称 “奚琴”“嵇琴”“胡琴”“南胡”，是中国传统拉弦乐器，已有一千多年历史。其琴体为木制，由琴筒、琴杆、琴轴、琴弦、千斤、琴码、琴弓等部件构成，张两根弦，音域达三个八度。二胡音色柔美、圆润、含蓄、浑厚，既能演奏细腻抒情的乐曲，也能表现欢快活泼的旋律，广泛用于独奏、合奏及戏曲伴奏，是中国民族音乐的代表性乐器之一。2021 年，上海市奉贤区二胡艺术入选国家级非物质文化遗产名录。'},
                  {title: '发展历史', content: '唐代称 “奚琴”，由北方少数民族发明，初为竹弓擦弦发声，后传入中原。宋代易名“嵇琴”，马尾琴弓出现，演奏技艺提升，沈括《梦溪笔谈》记载北宋艺人单弦演奏嵇琴的高超技巧,元代广泛流行于宴乐，《元史・礼乐志》记载其形制；明清时成为戏曲伴奏核心乐器，衍生出板胡、京胡等分支。20 世纪 20 年代，刘天华融合西方音乐理论，确立二胡五把位体系，创作《病中吟》《光明行》等独奏曲，推动其登上独奏舞台。'},
                  {title: '制作流程', content: '琴筒多用红木、紫檀木，蒙以蛇皮（优质者为野生蟒蛇皮），音窗雕花装饰,琴杆以乌木、红木制成，琴轴分木轴与金属轴，琴弦现多用钢弦或尼龙弦。'},
                  {title: '声色特点', content: '低音区深沉饱满，中音区温润流畅，高音区清亮通透'},
                  {title: '代表作品',content: '《病中吟》、《空山鸟语》、《二泉映月》、《听松》'}
                ],
                [
                  {title: '简介', content: '又称 “梆子胡”“胡呼”“瓢” 等。其琴体由琴筒、琴杆、琴弦、琴弓等部件构成，琴筒多用椰壳、竹筒或木料制成，前口蒙桐木板，通过马尾弓摩擦琴弦发声。板胡音色高亢明亮、穿透力强，擅长表现热烈激昂或细腻委婉的音乐情绪，广泛用于北方戏曲（如秦腔、豫剧、河北梆子）伴奏及民族管弦乐合奏，是中国北方民间音乐的代表性乐器。2019 年，秦腔板胡制作技艺入选西安市非物质文化遗产名录。'},
                  {title: '发展历史', content: '板胡起源于明末清初，伴随梆子腔戏曲的兴起而流行，最初主要用于陕西、河南等地的地方戏伴奏。清代文献记载其形制与演奏方式，初期琴筒用竹筒或椰壳，琴弦为丝弦，定弦与二股弦相近。新中国成立后，板胡形制改良：琴杆改用紫檀、酸枝木，琴弦换为钢丝弦，增设共鸣箱底托，音域扩展至两个半八度。20 世纪 70 年代后，出现双千斤、三弦板胡等改良版本，音域进一步拓宽。'},
                  {title: '制作流程', content: '琴筒:多用椰壳（秦腔板胡）、桐木或竹筒，前口蒙桐木板，背面雕刻音窗。琴杆：以小叶紫檀、酸枝木为佳，需自然干燥数年以防变形;琴弦：钢丝弦（高音）或缠弦（低音），琴弓用马尾制成'},
                  {title: '声色特点', content: '高音板胡音色清脆锐利，适合表现激昂情绪（如《大起板》）,中音板胡（如秦腔板胡）音色醇厚，兼具穿透力与抒情性'},
                  {title: '代表作品',content: '《大起板》、《红军哥哥回来了》、《王宝钏》'}
                ],
                [
                  {title: '简介', content: '蒙古族传统拉弦乐器，因琴首雕刻马头而得名，蒙古语称 “潮儿”。琴体由共鸣箱、琴杆、琴弦和琴弓构成，共鸣箱呈梯形，蒙马皮或羊皮，张两根马尾弦。其音色浑厚悠扬、深沉粗犷，既能表现草原的辽阔与牧民的生活，也能传达激昂或悲伤的情感，是蒙古族音乐文化的象征。2006 年，蒙古族马头琴音乐入选国家级非物质文化遗产名录；2011 年，马头琴制作技艺亦被列入国家级非遗。'},
                  {title: '发展历史', content: '马头琴起源可追溯至唐宋时期，与奚琴、马尾胡琴等拉弦乐器相关，最初用于游牧民族的歌舞伴奏.元代时进入宫廷，成为重要乐器，明清时期进一步发展，广泛流行于蒙古族民间。19 世纪前，琴体较小（称 “勺形胡琴”），音量有限；19 世纪末琴首固定为马头造型，琴体增大，音域扩展至三个八度。'},
                  {title: '制作流程', content: '琴箱：色木、榆木等硬杂木制成框架，正背两面蒙马皮或羊皮，绘民族图案。琴头与琴杆：整块木料雕刻，琴头为马头造型，琴杆前平后圆，设山口与琴马。琴弦：马尾搓成（里弦 40 根，外弦 60 根），琴弓用藤条或木料配马尾毛。'},
                  {title: '声色特点', content: '低音区深沉如草原长调，中音区圆润抒情，高音区清亮激昂。双面蒙皮结构使音色兼具穿透力与柔和感，适合独奏或伴奏'},
                  {title: '代表作品',content: '《朱色烈》、《八雅铃》、《达斡尔民歌主题协奏曲》'}
                ],
                [
                  {title: '简介', content: '中国传统拉弦乐器，因主要为京剧伴奏而得名，是京剧文场乐队的核心乐器。琴体由琴杆、琴筒、琴弦、琴轴等构成，琴筒蒙蛇皮，张两根丝弦，琴弓以马尾制成。其音色高亢清亮、穿透力强，既能烘托京剧唱腔的婉转悠扬，又能表现戏剧情节的激昂悲愤，是京剧艺术的重要组成部分。'},
                  {title: '发展历史', content: '京胡起源于清乾隆年间（18 世纪末），由胡琴改制而成，最初用于徽班进京后的京剧伴奏。早期京胡琴杆短、琴筒小，使用软弓演奏，音色较柔和；19世纪后逐渐改用硬弓，琴体增大，音域扩展。20 世纪上半叶，京剧演员降低音高追求圆润唱腔，京胡结构进一步调整，琴杆、琴筒加长，定弦方式多样化（如西皮、二黄等）'},
                  {title: '制作流程', content: '琴杆：精选福建闽侯产紫竹，需两年生向阳竹材，竹质紧实、纹理美观。琴筒：乌鞘蛇皮蒙制，取背部最佳部位，经阴干、烘烤定型；琴筒框架用紫竹或罗汉竹。其他部件：琴轴用乌木雕刻，琴弦为丝弦，琴弓以藤条配马尾。'},
                  {title: '声色特点', content: '竹制琴杆与蛇皮蒙皮赋予其穿透力极强的音色，适合京剧唱腔的高音域表现。通过托、垫、保、裹等伴奏技法，可模仿人声、自然声响（如鸟鸣、马嘶），烘托戏剧情感。'},
                  {title: '代表作品',content: '《奇袭白虎团》、《红云岗》、《小开门》、《柳青娘》'}
                ],
                [
                  {title: '简介', content: '中国少数民族传统乐器，因演奏时需拉动簧片尾部的细绳而得名，是世界上最小的民族乐器之一。其形制小巧，通常由竹片或金属片制成，簧片中部刻有舌簧，通过手指牵拉细绳使簧片振动发声，辅以口腔共鸣控制音高和音色。它既是情感表达的工具，也是文化传承的载体，广泛流传于彝族、羌族、撒拉族、回族等少数民族聚居区。'},
                  {title: '发展历史', content: '拉线口弦起源可追溯至新石器时代，最早以树叶、竹片或骨片为材料（如陕西石峁遗址出土的骨制口弦，距今 4000 余年）,20 世纪后，口弦因制作工艺复杂、现代文化冲击而濒危，部分技艺被列入国家级或省级非遗名录（如四川布拖彝族口弦、黑龙江赫哲族口弦）'},
                  {title: '制作流程', content: '选材：精选高山箭竹或金竹，自然风干后用油浸泡两年以增强韧性,雕刻簧片：将竹片削成 10~15 厘米长条形，中部镂空雕刻 “凸” 字形舌簧，尖端留孔穿线,'},
                  {title: '声色特点', content: '音域与表现力：单簧片仅能发一个基音，但通过口腔开合、气息强弱和牵拉力度变化，可产生丰富泛音和颤音，音域达八度以上,即兴性强：无固定曲谱，演奏者通过节奏变化和情感表达即兴创作，适合独奏或与民歌配合。'},
                  {title: '代表作品',content: '《彝族口弦》、《口弦情》、羌族《吆羊歌》、《薅草歌》、撒拉族《枕头琴调》'}
                ],
                [
                  {title: '简介', content: '中国汉族传统弓弦乐器，因拥有三根琴弦而得名，形制与二胡相似但结构更复杂。其琴筒多为木制，蒙以蛇皮或蟒皮，琴杆细长，上设三个琴轴分别控制三根弦（外弦、里弦 1、里弦 2），弓毛置于三弦之间，通过推拉琴弓摩擦琴弦发声。三胡音域宽广，兼具高、中、低音，可独奏、伴奏或参与民族乐队合奏。'},
                  {title: '发展历史', content: '三胡起源尚无明确史料记载，推测与胡琴类乐器发展相关。明清时期，胡琴家族逐渐分化出二胡、中胡等，三胡可能是在此基础上的改良或分支。现代三胡的形制可能受少数民族乐器影响（如傣族“勒巴”），但汉族三胡更注重音域扩展与音色融合。20 世纪以来，三胡因音域宽广、表现力强，被用于戏曲伴奏（如京剧、地方戏）和民族音乐创作，成为中国传统弓弦乐器中的独特分支'},
                  {title: '制作流程', content: '琴筒：木制（如红木、乌木），一端蒙蛇皮或蟒皮，另一端镂空共鸣。琴杆：竹制或木制，长约 80 厘米，顶端设三个琴轴（高音、中音、低音）。琴弦：外弦（钢丝弦）音色明亮，里弦 1（丝弦或尼龙弦）中音柔和，里弦 2（粗丝弦）低音浑厚。琴码与弓毛：琴码呈三角形设计，三根弦高低错位放置；弓毛位于三弦之间，通过角度变化控制单弦发声。'},
                  {title: '声色特点', content: '音域宽广：三根弦覆盖高、中、低音区，音域达三个八度以上，可演奏复杂旋律。'},
                  {title: '代表作品',content: '《二泉映月》、《赛马》'}
                ],
                [
                  {title: '简介', content: '又称坠子、坠弦，是中国汉族传统拉弦乐器，因形制源自小三弦并保留其指板而得名。琴体由琴杆、琴筒（共鸣箱）、琴弦和琴弓组成，琴筒蒙桐木板，琴杆较长，张两弦，以马尾弓拉奏。坠琴音色浑厚高亢，兼具柔美与穿透力，擅长模仿人声、动物鸣叫及戏曲拖腔，是河南坠子书、山东吕剧等地方曲艺的核心伴奏乐器，也可独奏或参与民族乐队合奏。'},
                  {title: '发展历史', content: '清代末年，河南说唱艺术 “莺歌柳书” 和 “三弦铰子书” 以小三弦伴奏，但弹拨音色与唱腔拖腔难以契合。民间艺人尝试在小三弦外弦与中弦间穿马尾弓拉奏（保留里弦共鸣），形成 “拉三弦”。20 世纪初，为适应女演员音域需求，艺人将琴筒蟒皮改为桐木板，去除里弦，定型为两弦拉奏的坠琴.'},
                  {title: '制作流程', content: '琴筒蒙皮需经温水浸泡后粘贴，确保振动均匀；琴杆木料需烘干至含水率 2% 以下防变形；定弦通过琴轴调节，传统凭经验，现代用校音器精准调音。'},
                  {title: '声色特点', content: '音域宽广：音域达两个半八度（a~d³），低音浑厚，高音清亮。'},
                  {title: '代表作品',content: '《庆丰收》、《新春乐》'} 
                ],
                 [
                  {title: '简介', content: '中国壮族传统拉弦乐器，因琴筒以马腿骨制成得名，壮语称 “冉列”“冉督” 或 “冉森”。其形制与二胡相似，但琴筒较短（长 10~20 厘米），琴杆稍细，张两弦，音色圆润、明亮、清脆，兼具穿透力与抒情性。马骨胡是壮剧、邕剧、布依戏及民间音乐的核心伴奏乐器，也是 “八音” 乐队的主奏乐器，流行于广西、贵州、云南等地的壮族聚居区，其中广西德保县被誉为 “广西马骨胡艺术之乡”。'},
                  {title: '发展历史', content: '马骨胡起源尚无定论，一说与宋代奚琴相关，早期用竹片轧弦，宋代后发展为马尾弓拉奏.清代乾隆年间（1736—1795年）已出现，最初用于节日串寨演奏，后融入壮剧伴奏。1815年广西隆林县壮剧团成立后，马骨胡成为乐队领奏乐器。'},
                  {title: '制作流程', content: '琴筒：取小马大腿骨，两端磨平，前口蒙蛇皮或蟒皮，后口镂空共鸣。琴杆：木制或竹制（红木、罗汉竹最佳），全长 46~60 厘米，顶端雕刻马头装饰。琴弦：内弦粗（羊肠或丝弦），外弦细（金属弦），下端固定于弦钩。琴弓：细竹为杆，马尾为毛，长约 60 厘米。'},
                  {title: '声色特点', content: '音域宽广：音域达两个半八度（a~d³），低音浑厚，高音清亮。'},
                  {title: '代表作品',content: '《赶坡》、《田林八音乐》'} 
                ],
                [
                  {title: '简介', content: '新疆维吾尔族、乌孜别克族等民族的传统弓弦乐器，因形制独特、音色多变而闻名。其琴体由琴杆、半球形琴筒（蒙马皮或驴皮）、主奏弦与共鸣弦组成，全长约 70~105 厘米。演奏时以马尾弓拉奏主奏弦，共鸣弦（5~14 根）通过振动增强音色层次。艾捷克广泛用于十二木卡姆、民间歌舞及达斯坦（叙事诗）伴奏，是维吾尔族音乐的核心乐器之一，也是新疆民族乐队的主奏拉弦乐器。'},
                  {title: '发展历史', content: '艾捷克起源尚无定论，主流观点认为源自波斯的卡曼恰（Kamāncheh），15世纪帖木儿帝国时期绘画中的乐器形制与多朗艾捷克相似。唐代至清代，通过丝绸之路传入新疆，融合突厥、中原等文化元素，逐步形成本土特色。清代被列入宫廷回部乐，称 “哈尔扎克,传统多朗艾捷克保留原始特征（如琴脚立地、共鸣弦多），20 世纪 50 年代改良艾捷克出现，扩大琴筒、取消共鸣弦，音域扩展至三个八度，更适合现代演奏。'},
                  {title: '制作流程', content: '琴体：琴杆用枣木、椰木制成，琴筒为半球形硬木，前口蒙马皮或驴皮，后口开音孔,琴弦：主奏弦 1~4 根（钢丝弦），共鸣弦 5~14 根（分置琴杆两侧）。装饰：琴头雕刻花纹，琴杆镶嵌骆驼骨或牦牛角，琴筒彩绘民族图案。'},
                  {title: '声色特点', content: '音域宽广：改良艾捷克音域达 g~g³，低音浑厚，高音清亮。'},
                  {title: '代表作品',content: '《拉克木卡姆》、《乌夏克木卡姆》'} 
                ],
                [
                  {title: '简介', content: '中国侗族传统拉弦乐器，因琴体细长形似牛大腿得名，侗语称 “郭各”“彦巴森”。其形制独特，全长 50~85 厘米，琴体用整段杉木挖凿成瓢状共鸣箱，蒙桐木面板，张两根弦（传统用棕绳或丝弦，现多用钢丝弦），琴弓以细竹系马尾制成。牛腿琴音色柔细、略带鼻音，擅长与人声融合，是侗族民歌、侗戏及 “牛腿琴歌” 的核心伴奏乐器，流行于贵州、广西、湖南三省区接壤的侗族聚居区。'},
                  {title: '发展历史', content: '牛腿琴起源无明确文献记载，民间传说与牛相关，但明代《行边记闻》中已有拉二弦的描述，可推测其雏形至少形成于明代。传统牛腿琴多用于 “行歌坐妹”（未婚青年社交）与侗戏伴奏，成为侗族文化生活的重要载体。'},
                  {title: '制作流程', content: '琴体：整段杉木挖凿成长瓢形腹腔，蒙桐木面板，琴背呈船底形，琴头方柱形无装饰，弦槽后开，两侧设硬木弦轴。琴弦：内弦粗（棕绳或丝弦），外弦细（金属弦），下端系于皮制缚弦。琴弓：细竹为杆，两端系棕丝或马尾，长 55~65 厘米。'},
                  {title: '声色特点', content: '传统音色：发音纤柔，略带嘶声与鼻音，音量较小但细腻，擅长表现侗族民歌的抒情性与叙事性。改良后特色：音量显著增强，高音清亮，中低音浑厚，音域扩展至两个八度以上，可独奏或与交响乐队协作。'},
                  {title: '代表作品',content: '《嘎郭各》、《嘎给以》'} 
                ],
                [
                  {title: '简介', content: '“独弦胡叮管堆”（意为孤儿琴），是佤族传统弓弦乐器，因仅有一根琴弦得名。其外形与二胡相似，琴杆竹制，琴筒蒙干笋，张一根尼龙弦或丝弦，琴弓以竹篾系棕丝或马尾制成。独弦琴音色低沉古朴，擅长表现抒情叙事性旋律，是佤族民歌、史诗《司岗里》及民间仪式的重要伴奏乐器，主要流行于云南普洱西盟县、沧源县等地，2022年入选云南省非物质文化遗产保护名录。'},
                  {title: '发展历史', content: '据佤族民间传说，独弦琴起源于孤儿艾叮为排解孤独，用母亲头发、竹片、笋叶等制成的乐器，后演变为倾诉情感的工具，故又称孤儿琴.现代改良增加 “千斤” 装置，优化共鸣箱结构，提升音域与表现力，可独奏或参与民族乐队演出,'},
                  {title: '制作流程', content: '琴杆：选弹性竹条制成扁方形柱状体，顶端设弦轴，下端插入琴筒。琴筒：大龙竹制成圆筒形，前口蒙干笋叶，后端敞口。琴弦：原用牛筋、藤皮捻制，现改用尼龙弦或丝弦。琴弓：竹篾为杆，两端系棕丝或马尾，长约 40 厘米。'},
                  {title: '声色特点', content: '传统音色：低沉浑厚，略带鼻音与颤音，如泣如诉，擅长表现忧郁、思念等情感。'},
                  {title: '代表作品',content: '《孤儿调》（诉说身世）、《串寨子调》（社交音乐）、《跳新房》（庆贺仪式）、《迎客调》（迎宾曲目）'} 
                ]
              ],
            items: [
