<template>
    <div id="main" class="chartTree"></div>
</template>

<script>
import * as echarts from 'echarts';
import localData from '@/assets/data.json';

export default{
    mounted(){
        let newPromise = new Promise((resolve) => {
            resolve()
        })
        newPromise.then(() => {
            this.initChart()
            // this.randerChart(localData)
        })
    },
    data(){
        return {
             myChart:null,
        }
    },
    methods: {
        initChart(){
        this.myChart = echarts.init(document.getElementById('main'))

        this.myChart.showLoading();
        try {
            const option = {
tooltip: {
        trigger: 'item',
        triggerOn: 'mousemove'
      },
      series: [
        {
          type: 'tree',
          data: [localData],
          left: '2%',
          right: '2%',
          top: '8%',
          bottom: '20%',
          symbol: 'emptyCircle',
          orient: 'vertical',
          expandAndCollapse: true,
          label: {
            position: 'top',
            rotate: -90,
            verticalAlign: 'middle',
            align: 'right',
            fontSize: 14
          },
          leaves: {
            label: {
              position: 'bottom',
              rotate: -90,
              verticalAlign: 'middle',
              align: 'left'
            }
          },
          animationDurationUpdate: 750
        }
      ]
    }
        option && this.myChart.setOption(option);
        }catch(error){
            console.error('图表初始化失败',error)
        }finally {
            this.myChart.hideLoading()
        }
                    
      }
    }
}
</script>

<style>
.chartTree {
    width: 95%;
    height: 95%;
    margin: 2.5%;
    background-color: rgba(255,255,255,0.7);
}
</style>