<template>
    <div id="main" class="chartTree"></div>
</template>

<script>
import * as echarts from 'echarts';
import localData from '@/assets/data_disktree.json';

export default {
    mounted() {
        new Promise((resolve) => {
            resolve()
        }).then(() => {
            this.initChart()
        })
    },
    data() {
        return {
            myChart: null,
        }
    },
    methods: {
        initChart() {
            this.myChart = echarts.init(document.getElementById('main'))
            this.myChart.showLoading();
            try {
                // 确保使用this访问方法
                const option = {
                    title: {
                        text: '乐器矩形树图',
                        left: 'center',
                        // legend: { show: false },
                    },
                    tooltip: {
                        formatter: function (info) {
                            let value = info.value;
                            let treePathInfo = info.treePathInfo;
                            let treePath = [];
                            for (let i = 1; i < treePathInfo.length; i++) {
                                treePath.push(treePathInfo[i].name);
                            }
                            return [
                                '<div class="tooltip-title">' +
                                // 确保formatUtil存在或移除相关代码
                                treePath.join('/') +
                                '</div>',
                                'Disk Usage: ' + value + ' KB'
                            ].join('');
                        }
                    },
                    series: [
                        {
                            name: '乐器',
                            type: 'treemap',
                            visibleMin: 300,
                            nodeClick: false,
                            roam: 'move',
                            label: {
                                show: true,
                                formatter: '{b}'
                            },
                            itemStyle: {
                                borderColor: '#fff'
                            },
                            data: localData
                        }
                    ]
                };
                // 使用this.myChart设置选项
                option && this.myChart.setOption(option);
            } catch (error) {
                console.error('图表初始化失败', error)
            } finally {
                this.myChart.hideLoading()
            }
        },
    }
}
</script>

<style lang="scss" scoped>
:deep(.chartTree) {
    width: 95%;
    height: 95%;
    margin: 2.5%;
    background-color: rgba(255,255,255,0.7);
}
</style>