      <p class="synopsis">
       我国吹管乐器起源甚古，相传在四千年前夏禹时期，就有一种用芦苇编排而成的吹管乐器叫作‘钥’。《诗经》中有箫、管、钥、埙、笙等乐器的记载。后来兴起的军乐，又叫鼓乐、横吹、骑吹等，是以排箫、笳、角、笛等为主要乐器，在民间婚丧喜庆及民俗节日中，吹管乐器俱是必不可少的乐器。
      </p>
